<template>
  <div>
    <div id="wrap" class="clearfix">
      <h1 class="userClass"></h1>
      <!---->
      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-select v-model="storeId" placeholder="">
              <el-option
                v-for="item in foodStoreInformtionList"
                :key="item.fd_sf_ID"
                :label="item.fd_sf_ProductName"
                :value="item.fd_sf_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search"></el-button>
            <el-button type="primary" @click="add"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->

      <el-table
        :data="foodStoreProductList"
        style="width: 100%">
        <el-table-column
          prop="fd_sf_ProductName"
          label=""
          align="center">
        </el-table-column>
        <el-table-column
          prop="fd_sfp_ID"
          label=""
          align="center">
        </el-table-column>
        <el-table-column
          prop="fd_sfp_Name"
          label=""
          align="center">
        </el-table-column>
        <el-table-column
          prop="fd_sfp_Price"
          label="()"
          align="center">
        </el-table-column>
        <el-table-column
          prop="fd_sfp_Remark"
          label=""
          align="center">
        </el-table-column>
        <el-table-column label="" align="center" width="300">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="update(scope.row)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="Delete(scope.row.fd_sfp_ID)">
            </el-button>
            <el-button
              size="mini"
              type="success"
              @click="Apply(scope.row.fd_sfp_ID)">
            </el-button>
          </template>
        </el-table-column>

      </el-table>

      <!---->

      <div class="block" style="text-align: right">
        <el-pagination
          :page-size="5"
          @current-change="handleCurrentChange"
          layout="prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>

      <!---->

      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="addOptions.fd_sfp_StoreFrontID" placeholder="">
              <el-option
                v-for="item in foodStoreInformtionList"
                :key="item.fd_sf_ID"
                :label="item.fd_sf_ProductName"
                :value="item.fd_sf_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.fd_sfp_Name"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.fd_sfp_Price"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.fd_sfp_Remark"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->

      <el-dialog title="" :visible.sync="updateDialog">
        <el-form :model="updateObj">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="updateObj.fd_sfp_StoreFrontID" placeholder="">
              <el-option
                v-for="item in foodStoreInformtionList"
                :key="item.fd_sf_ID"
                :label="item.fd_sf_ProductName"
                :value="item.fd_sf_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateObj.fd_sfp_Name"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateObj.fd_sfp_Price"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateObj.fd_sfp_Remark"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateDialog = false"> </el-button>
          <el-button type="primary" @click="updateSubmit"> </el-button>
        </div>
      </el-dialog>


    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([
      'foodStoreInformtionList',
      'foodStoreProductList'
    ]),
    data() {
      return {
        storeId: '',
        addDialog: false,
        addOptions: {
          "fd_sfp_StoreFrontID": "",//
          "fd_sfp_Name": "",//
          "fd_sfp_Price": "",//
          "fd_sfp_Remark": "",//
        },
        formLabelWidth: '120px',
        total: 0,
        updateObj: {},
        updateDialog: false,
      }
    },
    methods: {
      //
      handleCurrentChange(num) {
        this.initData(this.storeId, num)
      },
      //
      initData(id, num) {
        if (!id) {
          this.$notify({
            message: '',
            type: 'error'
          })
          return;
        }
        let selectStoreFrontProductInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "fd_sfp_ID": "",//
          "fd_sfp_StoreFrontID": id,//
          "fd_sfp_Name": "",//
          "priceFrom": "",//
          "priceTo": "",//
          "page": num ? num : 1,
          "rows": "5",
        };
        this.$store.dispatch('initFoodStoreProduct', selectStoreFrontProductInfo)
          .then(total => {
            this.total = total;
          }, err => {
            $notify({
              message: err,
              type: 'error'
            })
          })
      },
      //
      search() {
        this.initData(this.storeId)
      },
      //
      add() {
        this.$store.commit('setTranstionFalse');
        this.addDialog = true;
      },
      //
      addSubmit() {
        let insertStoreFrontProductInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.addOptions
        };
        this.$store.dispatch('addFoodStoreProduct', insertStoreFrontProductInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            })
            this.initData(this.storeId)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            })
          })
        this.addDialog = false;
      },
      //
      update(rowData) {
        this.updateObj = rowData;
        this.$store.commit('setTranstionFalse');
        this.updateDialog = true;
      },
      //
      updateSubmit() {
        let updateStoreFrontProductInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.updateObj
        };
        this.$store.dispatch('updateFoodStoreProduct', updateStoreFrontProductInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            })
            this.initData(this.storeId)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            })
          })
        this.updateDialog = false;
      },
      //
      Delete(id) {
        let deleteStoreFrontProductInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "fd_sfp_ID": id ? id : '',//
          }
        };
        this.$store.dispatch('deleteFoodStoreProduct', deleteStoreFrontProductInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            })
            this.initData(this.storeId)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            })
          })
      },
      //
      Apply(id) {
        let insertPageIntroduceInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "fd_pi_StoreID": id ? id : '',//
          }
        };
        this.$store.dispatch('applyRecommendFood',insertPageIntroduceInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.storeId);
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      }
    },
  }
</script>
<style scoped>
</style>



