<template>
  <div>
    <div id="wrap" class="clearfix">
      <h1 class="userClass"></h1>

      <!---->

      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-select v-model="storeId" placeholder="">
              <el-option
                v-for="item in foodStoreInformtionList"
                :key="item.fd_sf_ID"
                :label="item.fd_sf_ProductName"
                :value="item.fd_sf_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search"></el-button>
            <el-button type="primary" @click="add"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->

      <el-table
        :data="foodStoreTableTimeList"
        style="width: 100%">
        <el-table-column
          prop="fd_clt_ID"
          label=""
          align="center">
        </el-table-column>
        <el-table-column
          prop="fd_sf_ProductName"
          label=""
          align="center">
        </el-table-column>

        <el-table-column
          prop="fd_clt_CanSellTime"
          label=""
          align="center">
        </el-table-column>


        <el-table-column
          prop="fd_clt_Remark"
          label=""
          align="center">
        </el-table-column>

        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="update(scope.row)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="Delete(scope.row.fd_clt_ID)">
            </el-button>
          </template>
        </el-table-column>

      </el-table>

      <!---->
      <div class="block" style="text-align: right">
        <el-pagination
          :page-size="5"
          @current-change="handleCurrentChange"
          layout="prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>

      <!---->

      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="addOptions.fd_clt_FrontID" placeholder="">
              <el-option
                v-for="item in foodStoreInformtionList"
                :key="item.fd_sf_ID"
                :label="item.fd_sf_ProductName"
                :value="item.fd_sf_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-time-select
              v-model="addOptions.fd_clt_CanSellTime"
              :picker-options="{
                start: '00:00',
                step: '01:00',
                end: '24:00'
              }"
              placeholder="">
            </el-time-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->

      <el-dialog title="" :visible.sync="updateDialog">
        <el-form :model="updateObj">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="updateObj.fd_clt_FrontID" placeholder="">
              <el-option
                v-for="item in foodStoreInformtionList"
                :key="item.fd_sf_ID"
                :label="item.fd_sf_ProductName"
                :value="item.fd_sf_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-time-select
              v-model="updateObj.fd_clt_CanSellTime"
              :picker-options="{
                start: '00:00',
                step: '01:00',
                end: '24:00'
              }"
              placeholder="">
            </el-time-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateDialog = false"> </el-button>
          <el-button type="primary" @click="updateSubmit"> </el-button>
        </div>
      </el-dialog>

    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([
      'foodStoreInformtionList',
      'foodStoreTableTimeList'
    ]),
    data() {
      return {
        storeId: '',
        total: 0,
        formLabelWidth: '120px',
        addDialog: false,
        updateDialog: false,
        addOptions: {
          "fd_clt_FrontID": "",//
          "fd_clt_CanSellTime": "",//
        },
        updateObj: {},
      }
    },
    methods: {
      //      
      handleCurrentChange(num) {
        this.initData(this.storeId, num);
      },
      //
      initData(id, num) {
        if (!id) {
          this.$notify({
            message: '',
            type: 'error'
          })
          return
        }
        let selectCanLockTimeInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "page": num ? num : 1,
          "rows": "5",
          "fd_clt_ID": "",//
          "fd_clt_FrontID": id,//
          "fd_clt_CanSellTime": "",//
        };
        this.$store.dispatch('initFoodStoreTableTime', selectCanLockTimeInfo)
          .then(total => {
            this.total = total;
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            })
          })
      },
      //
      search() {
        this.initData(this.storeId);
      },
      //
      add() {
        this.$store.commit('setTranstionFalse');
        this.addDialog = true;
      },
      //
      addSubmit() {
        let insertCanLockTimeInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.addOptions
        };
        this.$store.dispatch('addFoodStoreTableTime', insertCanLockTimeInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            })
            this.initData(this.storeId);
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            })
          })
        this.addDialog = false;
      },
      //
      update(rowData) {
        this.updateObj = rowData;
        this.$store.commit('setTranstionFalse');
        this.updateDialog = true;
      },
      //
      updateSubmit() {
        let updateCanLockTimeInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.updateObj
        };
        this.$store.dispatch('updateFoodStoreTableTime', updateCanLockTimeInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            })
            this.initData(this.storeId);
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            })
          })
        this.updateDialog = false;
      },
      //
      Delete(id) {
        let deleteCanLockTimeInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "fd_clt_ID": id,//
          }
        };
        this.$store.dispatch('deleteFoodStoreTableTime',deleteCanLockTimeInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            })
            this.initData(this.storeId);
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            })
          })
      },
    },
  }
</script>
<style scoped>
</style>



