<template>
  <!--  y  -->
  <div id="wrap" class="clearfix">
    <div class="title clearfix" style="padding: 20px">
      <h1 style="font-size: 20px;"></h1>
      <!---->
      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-select v-model="storeId" placeholder="">
              <el-option
                v-for="item in foodStoreInformtionList"
                :key="item.fd_sf_ID"
                :label="item.fd_sf_ProductName"
                :value="item.fd_sf_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search"></el-button>
            <el-button type="primary" @click="add"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </div>

    <!---->
    <el-table
      :data="foodStoreRoomList"
      style="width: 100%">

      <el-table-column
        prop="fd_sf_ProductName"
        label=""
        align="center">
      </el-table-column>

      <el-table-column
        prop="fd_sfr_RoomName"
        label=""
        align="center">
      </el-table-column>


      <el-table-column
        prop="fd_sfr_BookMoney"
        label="()"
        align="center">
      </el-table-column>

      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="update(scope.row)">
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="Delete(scope.row.fd_sfr_ID)">
          </el-button>
        </template>
      </el-table-column>

    </el-table>

    <!---->
    <div class="block" style="text-align: right">
      <el-pagination
        :page-size="5"
        @current-change="handleCurrentChange"
        layout="prev, pager, next"
        :total="total"
        v-show="total"
      >
      </el-pagination>
    </div>

    <!---->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="addOptions">

        <el-form-item label="" :label-width="formLabelWidth" style="width: 55%">
          <el-select v-model="addOptions.fd_sfr_StoreFrontID" placeholder="">
            <el-option
              v-for="item in foodStoreInformtionList"
              :key="item.fd_sf_ID"
              :label="item.fd_sf_ProductName"
              :value="item.fd_sf_ID">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth" style="width: 55%">
          <el-input v-model="addOptions.fd_sfr_RoomName" auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="()" :label-width="formLabelWidth" style="width: 55%">
          <el-input v-model="addOptions.fd_sfr_BookMoney" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="addSubmit"> </el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="updateDialog">
      <el-form :model="updateObj">
        <el-form-item label="" :label-width="formLabelWidth" style="width: 55%">
          <el-select v-model="updateObj.fd_sfr_StoreFrontID" placeholder="">
            <el-option
              v-for="item in foodStoreInformtionList"
              :key="item.fd_sf_ID"
              :label="item.fd_sf_ProductName"
              :value="item.fd_sf_ID">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" style="width: 55%">
          <el-input v-model="updateObj.fd_sfr_RoomName" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="()" :label-width="formLabelWidth" style="width: 55%">
          <el-input v-model="updateObj.fd_sfr_BookMoney" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="updateDialog = false"> </el-button>
        <el-button type="primary" @click="updateSubmit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([
      'foodStoreRoomList',
      'foodStoreInformtionList'
    ]),
    data() {
      return {
        addOptions: {
          "fd_sfr_StoreFrontID": "",//
          "fd_sfr_RoomName": "",//
          "fd_sfr_BookMoney": ""//
        },
        dialogFormVisible: false,
        formLabelWidth: '120px',
        storeId: '',
        total: 0,
        updateObj: {},
        updateDialog: false,
      }
    },
    methods: {
      //      
      handleCurrentChange(num) {
        this.initData('', num);
      },
      //
      initData(id) {
        if (!id) {
          this.$notify({
            message: '',
            type: 'error'
          })
          return;
        }
        let initStoreRoom = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "fd_sfr_ID": "",//
          "fd_sfr_StoreFrontID": id,//
          "fd_sfr_RoomName": "",//
          "page": "1",
          "rows": "10",
        }
        this.$store.dispatch('initFoodStoreRoom', initStoreRoom).then(
          total => {
            this.total = total;
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },

      //
      search() {
        this.initData(this.storeId)
      },
      //
      add() {
        this.dialogFormVisible = true;
        this.$store.commit('setTranstionFalse');
      }
      ,
      //
      addSubmit() {
        let addStoreRoom = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.addOptions
        };
        this.$store.dispatch('addFoodStoreRoom', addStoreRoom)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.storeId)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.dialogFormVisible = false;
      }
      ,
      //
      update(rowData) {
        this.updateObj = rowData;
        this.$store.commit('setTranstionFalse');
        this.updateDialog = true;
      }
      ,
      //
      updateSubmit() {
        let updateStoreFrontRoomInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.updateObj
        };
        this.$store.dispatch('updateFoodStoreRoom', updateStoreFrontRoomInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.storeId)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.updateDialog = false;
      }
      ,
      //
      Delete(id) {
        let deleteStoreRoom = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "fd_sfr_ID": id ? id : '',//
          }
        };
        this.$store.dispatch('deleteFoodStoreRoom', deleteStoreRoom).then(
          suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.storeId)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      }
    }
  }
</script>

<style scoped>
</style>



