<template>
  <div>
    <section id="wrap">
      <h1 class="userClass"></h1>
      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-input type="text" v-model="userName" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search"></el-button>
            <!--<el-button type="primary" @click="next"></el-button>-->
            <!--<el-button type="primary" @click="addAdminBusinessInformation"></el-button>-->
          </el-form-item>
        </el-form>
      </el-col>

      <el-table
        :data="adminBusinessInformationList"
        highlight-current-row
        v-loading="isLoading"
        style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label=":">
                <span>{{ props.row.ts_tb_UserID }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.ts_tb_Name }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.ts_tb_FullName }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.ts_tb_CertNo }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.ts_tb_TelePhone }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.ts_tb_FixedPhone }}</span>
              </el-form-item>
              <el-form-item label=":">
                <el-popover
                  ref="popover1"
                  placement="top-start"
                  trigger="hover"
                  :content="props.row.ts_tb_Address">
                </el-popover>
                <el-button v-popover:popover1></el-button>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.ts_tb_Country }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.ts_tb_Provice + props.row.ts_tb_City + "" + props.row.ts_tb_Contry + ""}}</span>
              </el-form-item>
              <el-form-item label=":">
                <!--<span>{{ props.row.ta_tg_Describe }}</span>-->
                <el-popover
                  ref="popover1"
                  placement="top-start"
                  trigger="hover"
                  :content="props.row.ts_tb_AddressDescribe">
                </el-popover>
                <el-button v-popover:popover1></el-button>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.ts_tb_Longitude + "" }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.ts_tb_Latitude + "" }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.ts_tb_Level }}</span>
              </el-form-item>
              <el-form-item label=":">
                <img :src="item" alt="" v-for="item in props.row.ts_tb_ShowImage"
                     style="width: 100px;height: 100px;margin-right: 10px">
                <!--<p v-for="item in props.row.ts_tb_ShowImage">{{ item }}</p>-->
              </el-form-item>
              <el-form-item label=":">
                <img :src="item" alt="" v-for="item in props.row.ts_tb_StoreImageURL"
                     style="width: 100px;height: 100px">
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.ts_tb_EndTime }}</span>
              </el-form-item>
              <!--<el-form-item label="html:">-->
              <!--<span>{{ props.row.ta_tg_Describe }}</span>-->
              <!--</el-form-item>-->
              <el-form-item label=":">
                <!--<span>{{ props.row.ta_tg_Remark }}</span>-->
                <el-popover
                  ref="popover1"
                  placement="top-start"
                  trigger="hover"
                  :content="props.row.ts_tb_Remark">
                </el-popover>
                <el-button v-popover:popover1></el-button>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.ts_tb_LicenceCode }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tb_StoreImageURL}}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.ts_tb_Bank}}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.ts_tb_CardNo}}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.ts_tb_CreateTime }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.ts_tb_Check | getCheck}}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="ts_tb_UserID">
        </el-table-column>
        <el-table-column
          label=""
          prop="ts_tb_Name">
        </el-table-column>
        <el-table-column
          label=""
          prop="ts_tb_TelePhone">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="updateAdminBusinessInformation(scope.row.ts_tb_UserID)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="deleteAdminBusinessInformation(scope.row.ts_tb_UserID )">
            </el-button>
            <el-button
              size="mini"
              @click="nextSearch(scope.row.ts_tb_UserID )">
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <div class="block" style="float: right;">
        <el-pagination
          @current-change="handleCurrentChange"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>
    </section>
    <el-dialog title="" :visible.sync="updateAdminBusinessInformationDialog">
      <el-form :model="updateAdminBusinessInformationObj">
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateAdminBusinessInformationObj.ts_tb_UserID" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateAdminBusinessInformationObj.ts_tb_Name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateAdminBusinessInformationObj.ts_tb_FullName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateAdminBusinessInformationObj.ts_tb_CertNo" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateAdminBusinessInformationObj.ts_tb_TelePhone" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateAdminBusinessInformationObj.ts_tb_FixedPhone" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateAdminBusinessInformationObj.ts_tb_Country" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateAdminBusinessInformationObj.ts_tb_Provice" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateAdminBusinessInformationObj.ts_tb_City" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateAdminBusinessInformationObj.ts_tb_Contry" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateAdminBusinessInformationObj.ts_tb_Address" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateAdminBusinessInformationObj.ts_tb_AddressDescribe" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" :required="isOff">
          <!--<el-input v-model="AddOptions.data.tm_ts_Longitude" auto-complete="off"></el-input>-->
          <el-button type="primary" @click="openMap"></el-button>
          <span
            style="padding-right: 20px">:{{updateAdminBusinessInformationObj.ts_tb_Longitude}}</span><span>:{{updateAdminBusinessInformationObj.ts_tb_Latitude}}</span>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateAdminBusinessInformationObj.ts_tb_Level" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateAdminBusinessInformationObj.ts_tb_LicenceCode" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateAdminBusinessInformationObj.ts_tb_Bank" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateAdminBusinessInformationObj.ts_tb_CardNo" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <a href="javascript:;" class="file">
            <input type="file" name="" ref="upload" accept="image/*" multiple>
          </a>
          <p v-for="item in ImageURL" v-show="ImageURL.length">{{item ? item : ""}}</p>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <a href="javascript:;" class="file">
            <input type="file" name="" ref="upload1" accept="image/*" multiple>
          </a>
          <p v-for="item in ImageURL1" v-show="ImageURL1.length">{{item ? item : ""}}</p>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-date-picker
            v-model="updateAdminBusinessInformationObj.ts_tb_EndTime"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-select v-model="updateAdminBusinessInformationObj.ts_tb_Check" placeholder="">
            <el-option
              v-for="item in checkOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <span>(0:,1:,2:)</span>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateAdminBusinessInformationObj.ts_tb_Remark" placeholder="" type="textarea"
                    :autosize="{ minRows: 6, maxRows: 10}"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="updateAdminBusinessInformationDialog = false"> </el-button>
        <el-button type="primary" @click="updateAdminBusinessInformationSubmit"> </el-button>
      </div>
    </el-dialog>


    <!---->
    <div class="mapJW" v-show="isMap">
      <div id="allmap">
      </div>
      <el-form ref="form" :model="form"
               style="z-index: 9990;position: absolute;background: #fff;bottom: 30px;left: 0;padding: 10px 30px;">
        <el-form-item label="">
          <el-input v-model="form.x"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.y"></el-input>
        </el-form-item>
        <el-button type="primary" @click="searchMap"></el-button>
        <el-button type="success" @click="mapSubmit"></el-button>
      </el-form>
    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    name: '',
    data() {
      return {
        checkOptions: [{
          value: '0',
          label: ''
        }, {
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }],
        isOff: true,
        isMap: false,
        formLabelWidth: '120px',
        userName: '',
        isLoading: false,
        ImageURL: [],
        ImageURL1: [],
        addAdminBusinessInformationDialog: false,//
        updateAdminBusinessInformationDialog: false,//
        addOptions: {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "phone": "",
          "validateNo": "",
          "userpassword": ""
        },
        total: 0,
        form: {
          x: '',
          y: ''
        }
      }
    },
    computed: mapGetters([
      'adminBusinessInformationList',
      'updateAdminBusinessInformationObj'
    ]),
    methods: {
      nextSearch(id) {
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "tradeID": id ? id : '',
          "userID": "",
          "pcName": "",
          "ID": '',
          'isDelete': 0,
          "page":  1,
          "rows": 5
        };

        this.$store.dispatch('initAdminTradeGoodList', options)
          .then((data) => {
            if (data.data.length) {
              this.$store.commit('adminMerchantProductsId', id);
              this.$router.push({name: 'AdminMerchantProducts'})
              sessionStorage.setItem('index', '1')
            }else{
              this.$notify({
                message: '',
                type: 'error'
              });
            }
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })


//        this.$store.commit('adminMerchantProductsId',id);
//        this.$router.push({name:'AdminMerchantProducts'})
//        sessionStorage.setItem('index','1')
      },
      //
      uploadImg(file) {
        return new Promise(function (relove, reject) {
          lrz(file)
            .then(data => {
              relove(data.base64.split(',')[1])
            })
        })
      },
      uploaNode() {
        setTimeout(() => {
          if (this.$refs.upload) {
            this.$refs.upload.addEventListener('change', data => {
              for (var i = 0; i < this.$refs.upload.files.length; i++) {
                this.uploadImg(this.$refs.upload.files[i]).then(data => {
                  this.$store.dispatch('uploadAdminImgs', {
                    imageData: data
                  })
                    .then(data => {
                      if (data) {
                        this.ImageURL.push(data.data);
                      } else {
                        this.$notify({
                          message: '!',
                          type: 'error'
                        });
                      }
                    })
                })
              }
            })
          }
          if (this.$refs.upload1) {
            this.$refs.upload1.addEventListener('change', data => {
              for (var i = 0; i < this.$refs.upload1.files.length; i++) {
                this.uploadImg(this.$refs.upload1.files[i]).then(data => {
                  this.$store.dispatch('uploadAdminImgs', {
                    imageData: data
                  })
                    .then(data => {
                      if (data) {
                        this.ImageURL1.push(data.data);
                      } else {
                        this.$notify({
                          message: '!',
                          type: 'error'
                        });
                      }
                    })
                })
              }
            })
          }
        }, 30)
      },
      //
      mapSubmit() {
        this.isMap = false;
        this.updateAdminBusinessInformationObj.ts_tb_Longitude = this.form.x;
        this.updateAdminBusinessInformationObj.ts_tb_Latitude = this.form.y;
      },
      //
      searchMap() {
        var map = new BMap.Map("allmap");
        map.centerAndZoom(new BMap.Point(116.331398, 39.897445), 11);
        map.enableScrollWheelZoom(true);
        map.clearOverlays();
        var new_point = new BMap.Point(this.form.x, this.form.y);
        var marker = new BMap.Marker(new_point);  // 
        map.addOverlay(marker);              // 
        map.panTo(new_point);
      },
      //
      openMap() {
        this.isMap = true
      },
//      
      initData(name, page) {
        let initOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "tbUserID": "",
          "tbName": name ? name : '',
          "isDelete": 0,
          "page": page ? page : 1,
          "rows": 5
        };
        this.isLoading = true;
        this.$store.dispatch('initAdminBusinessInformation', initOptions)
          .then((totalrows) => {
            this.total = totalrows;
            this.isLoading = false;
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      //
      search() {
        this.initData(this.userName.trim(), 1)
      },
      //
      handleCurrentChange(num) {
        this.initData(this.userName.trim(), num)
      },
      //
//      addAdminBusinessInformation(){
//        this.$store.commit('setTranstionFalse');
//        this.addAdminBusinessInformationDialog = true;
//      },
      //
      updateAdminBusinessInformation(id) {
        this.$store.commit('setTranstionFalse');
        this.updateAdminBusinessInformationDialog = true;
        this.$store.commit('initUpdateAdminBusinessInformationObj', id)
        this.uploaNode()
      },
      //
      updateAdminBusinessInformationSubmit() {
        this.updateAdminBusinessInformationObj.ts_tb_ShowImage = this.ImageURL.join(',');
        this.updateAdminBusinessInformationObj.ts_tb_StoreImageURL = this.ImageURL1.join(',');
        let updateOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.updateAdminBusinessInformationObj
        };
        this.$store.dispatch('UpdateAdminBusinessInformation', updateOptions)
          .then(() => {
            this.$notify({
              message: '!',
              type: 'success'
            });
            this.initData(this.userName.trim(), 1)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          });
        this.updateAdminBusinessInformationDialog = false;
      },
      //
      deleteAdminBusinessInformation(id) {
        let deleteOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "tbUserID": id
        };
        this.$store.dispatch('DeleteAdminBusinessInformation', deleteOptions)
          .then(() => {
            this.$notify({
              message: '!',
              type: 'success'
            });
            this.initData(this.userName.trim(), 1)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          });
      }
    },
    mounted() {
      var _this = this;
      var geolocation = new BMap.Geolocation();
      geolocation.getCurrentPosition(function (r) {
        _this.form.x = r.point.lng;
        _this.form.y = r.point.lat;
        if (this.getStatus() == BMAP_STATUS_SUCCESS) {
          var map = new BMap.Map("allmap");
          // var point = new BMap.Point(116.400244, 39.92556);
          map.centerAndZoom(r.point, 12); //
          map.enableScrollWheelZoom(true); //
          var marker = new BMap.Marker(r.point);// 

          map.addOverlay(marker);  // 
          map.panTo(r.point);
          // alert('' + r.point.lng + ',' + r.point.lat);

          marker.enableDragging(); //
          //marker.disableDragging();           // 

          // 
          marker.addEventListener("dragend", function (e) {

            var x = e.point.lng; //
            var y = e.point.lat; //
//            alert("" + x + "" + y);
            _this.form.x = x;
            _this.form.y = y;
          });
        }
        else {
          alert('failed' + this.getStatus());
        }
      }, {enableHighAccuracy: true})
    },
  }
</script>
<style scoped>

  #allmap {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 9900;
    margin-top: -50px;
    font-family: "";
  }
</style>



