<template>
  <div id="wrap" class="clearfix">
    <h1 class="userClass"></h1>
    <el-col :span="24" class="formSearch">
      <el-form :inline="true">
        <el-form-item>
          <span>:</span>
        </el-form-item>
        <el-form-item>
          <el-input type="text" v-model="roomName" auto-complete="off" placeholder="" size="small"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search" size="small"></el-button>
          <el-button type="primary" @click="Add" size="small"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-table
      :data="hotelRoomProductList"
      v-loading="isLoading"
      style="width: 100%"
    >
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label=":">
              <span>{{ props.row.ht_rpp_BedType | getBedType}}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.ht_rpp_BreakfastType | getBreakfastType}}</span>
            </el-form-item>
            <el-form-item label="WAFI:">
              <span>{{ props.row.ht_rpp_WafiType | getWafiType}}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.ht_rpp_CancelType | getCancelType}}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.ht_rpp_RoomNumber }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column
        width="100"
        prop="ht_rpp_ID"
        align="center"
        label="ID"
      >
      </el-table-column>
      <el-table-column
        width="140"
        prop="ht_rpp_Name"
        align="center"
        label=""
      >
      </el-table-column>
      <el-table-column
        width="140"
        prop="ht_rpp_ProductPrice"
        align="center"
        label="()"
      >
      </el-table-column>
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="Update(scope.row.ht_rpp_ID)">
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="Delete(scope.row.ht_rpp_ID)">
          </el-button>
          <el-button
            size="mini"
            type="primary"
            @click="toRoomEntity(scope.row)">
          </el-button>
          <el-button
          size="mini"
          type="primary"
          @click="toLotRoomNumber(scope.row.ht_rpp_ID)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <div class="block" style="float: right;">
      <el-pagination
        @current-change="handleCurrentChange"
        :page-size="5"
        layout="total, prev, pager, next"
        :total="total"
        v-show="total"
      >
      </el-pagination>
    </div>
    <!---->
    <el-dialog title="" :visible.sync="addDialog">
      <el-form :model="addOptions">
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="addOptions.data.ht_rpp_Name"></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-select v-model="addOptions.data.ht_rpp_BedType" placeholder="">
            <el-option
              v-for="item in BedTypeList"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-select v-model="addOptions.data.ht_rpp_BreakfastType" placeholder="">
            <el-option
              v-for="item in BreakfastTypeList"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="WAFI:" :label-width="formLabelWidth">
          <el-select v-model="addOptions.data.ht_rpp_WafiType" placeholder="">
            <el-option
              v-for="item in WAFIList"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-select v-model="addOptions.data.ht_rpp_CancelType" placeholder="">
            <el-option
              v-for="item in CancelTypeList"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="addOptions.data.ht_rpp_ProductPrice"></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="addOptions.data.ht_rpp_RoomNumber"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addDialog = false"> </el-button>
        <el-button type="primary" @click="addSubmit"> </el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="updateDialog">
      <el-form :model="updateHotelRoomProductObj">
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateHotelRoomProductObj.ht_rpp_Name"></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-select v-model="updateHotelRoomProductObj.ht_rpp_BedType" placeholder="">
            <el-option
              v-for="item in BedTypeList"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-select v-model="updateHotelRoomProductObj.ht_rpp_BreakfastType" placeholder="">
            <el-option
              v-for="item in BreakfastTypeList"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="WAFI:" :label-width="formLabelWidth">
          <el-select v-model="updateHotelRoomProductObj.ht_rpp_WafiType" placeholder="">
            <el-option
              v-for="item in WAFIList"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-select v-model="updateHotelRoomProductObj.ht_rpp_CancelType" placeholder="">
            <el-option
              v-for="item in CancelTypeList"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateHotelRoomProductObj.ht_rpp_ProductPrice"></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateHotelRoomProductObj.ht_rpp_RoomNumber"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="updateDialog = false"> </el-button>
        <el-button type="primary" @click="updateSubmit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default{
    name: '',
    data(){
      return {
        roomName: '',
        total: 0,
        roomID: '',
        isLoading: false,
        addDialog: false,
        updateDialog: false,
        formLabelWidth: '120px',
        //
        BedTypeList: [
          {
            name: '',
            value: '1'
          },
          {
            name: '',
            value: '2'
          },
          {
            name: '',
            value: '3'
          },
        ],
        //
        BreakfastTypeList: [
          {
            name: '',
            value: '0'
          },
          {
            name: '',
            value: '1'
          },
          {
            name: '',
            value: '2'
          },
          {
            name: '',
            value: '3'
          },
          {
            name: '',
            value: '4'
          },
        ],
        //WAFI
        WAFIList: [
          {
            name: 'WAFI',
            value: '0'
          },
          {
            name: '',
            value: '1'
          },
          {
            name: '',
            value: '2'
          },
        ],
        //
        CancelTypeList: [
          {
            name: '',
            value: '0'
          },
          {
            name: '',
            value: '1'
          }
        ],
        addOptions: {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "lb",
          "pcName": "",
          "data": {
            "ht_rpp_RoomID": "",//
            "ht_rpp_Name": "",//
            "ht_rpp_BedType": "",// 1 2 3
            "ht_rpp_BreakfastType": "",//0  1  2  3  4 
            "ht_rpp_WafiType": "",//0WAFI  1 2
            "ht_rpp_CancelType": "",//
            "ht_rpp_ProductPrice": "",//
            "ht_rpp_RoomNumber": "",//
          }
        }
      }
    },
    computed: mapGetters([
      'hotelRoomProductList',
      'updateHotelRoomProductObj'
    ]),
    created(){
      let id = this.$route.params.id;
      if (!id) {
        this.$notify({
          message: '!',
          position: 'top-left',
          type: 'error'
        });
        this.$router.push({name: 'HotelRoom'});
        return
      }
      this.roomID = id;
      this.initData('', 1)
    },
    methods: {
      //
      toRoomEntity(item){
        sessionStorage.setItem('roomProductID',item.ht_rpp_ID)
        this.$router.push({name:'HotelRoomEntity', params: {id: item.ht_rpp_ID}})
      },
      toLotRoomNumber(id){
        this.$router.push({name:'HotelLotRoomNumber', params: {id: id}})
      },
      //
      toRoomNumber(RoomProductID){
        this.$router.push({name:'HotelRoomNumber', params: {id: RoomProductID}})
      },
      //
      handleCurrentChange(num){
        this.initData(this.roomName, num);
      },
      //
      initData(name, page){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "ht_rpp_ID": "",//ID
          "ht_rpp_RoomID": this.roomID,//
          "ht_rpp_Name": name ? name : '',//
          "ht_rpp_BedType": "",// 1 2 3
          "ht_rpp_BreakfastType": "",//0  1  2  3  4 
          "ht_rpp_WafiType": "",//0WAFI  1 2
          "ht_rpp_CancelType": "",//
          "ht_rpp_ProductPrice": "",//
          "page": page ? page : 1,//
          "rows": "5",//
        };
        this.isLoading = true;
        this.$store.dispatch('initHotelRoomProduct', options)
        .then((total) => {
          this.total = total
          this.isLoading = false
        }, err => {
          this.$notify({
            message: err,
            type: 'error'
          });
        })
      },
      //
      search(){
        this.initData(this.roomName, 1)
      },
      //
      Add(){
        this.$store.commit('setTranstionFalse');
        this.addDialog = true;
      },
      //
      addSubmit(){
        this.addOptions.data.ht_rpp_RoomID = this.roomID;
        this.$store.dispatch('AddHotelRoomProduct', this.addOptions)
        .then(suc => {
          this.$notify({
            message: suc,
            type: 'success'
          });
          this.initData(this.roomName, 1)
        }, err => {
          this.$notify({
            message: err,
            type: 'error'
          });
        });
        this.addDialog = false;
      },
      Update(id){
        this.$store.commit('setTranstionFalse');
        this.updateDialog = true;
        this.$store.commit('UpdateHotelRoomProduct', id)
      },
      //
      updateSubmit(){
        let updateOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.updateHotelRoomProductObj
        }
        this.$store.dispatch('UpdateHotelRoomProduct',updateOptions)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.roomName, 1)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          });
        this.updateDialog = false;
      },
      //
      Delete(id){
        let deleteOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "lb",
          "pcName": "",
          "data": {
            "ht_rpp_ID":id//ID
          }
        };
        this.$store.dispatch('DeleteHotelRoomProduct',deleteOptions)
        .then(suc => {
          this.$notify({
            message: suc,
            type: 'success'
          });
          this.initData(this.roomName, 1)
        }, err => {
          this.$notify({
            message: err,
            type: 'error'
          });
        });
      },
    },
  }
</script>
<style scoped>

</style>



