<template>
  <div id="wrap" class="clearfix">
    <h1 class="userClass"></h1>
    <el-col :span="24" class="formSearch">
      <el-form :inline="true">
        <el-form-item>
          <!--<span>:</span>-->
        </el-form-item>
        <el-form-item>
          <!--<el-input type="text" v-model="roomName" auto-complete="off" placeholder="" size="small"></el-input>-->
        </el-form-item>
        <el-form-item  style="float: right;">
          <!--<el-button type="primary" @click="search" size="small"></el-button>-->
          <el-button type="primary" @click="Add" size="small"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-table
      :data="hotelRoomRoomFacilitiesList"
      v-loading="isLoading"
      style="width: 100%"
    >
      <el-table-column
        prop="ht_rth_ID"
        label="ID"
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="ht_rth_RoomHardName"
        label=""
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="ht_rth_RoomHardTypeName"
        label=""
        align="center"
      >
      </el-table-column>
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="Update(scope.row.ht_rth_ID)">
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="Delete(scope.row.ht_rth_ID)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <div class="block" style="float: right;">
      <el-pagination
        @current-change="handleCurrentChange"
        :page-size="5"
        layout="total, prev, pager, next"
        :total="total"
        v-show="total"
      >
      </el-pagination>
    </div>

    <!---->
    <el-dialog title="" :visible.sync="addDialog">
      <el-form :model="addOptions">

        <el-form-item label=":" :label-width="formLabelWidth">
          <el-select v-model="RoomFacilitiesTypeID" placeholder="" @change="changeRoomFacilities">
            <el-option
              v-for="item in hotelRoomFacilitiesTypeList"
              :key="item.ht_rht_ID"
              :label="item.ht_rht_Name"
              :value="item.ht_rht_ID">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-select v-model="addOptions.data.ht_rth_RoomHardID" placeholder="" >
            <el-option
              v-for="item in roomFacilitiesList"
              :key="item.ht_rh_ID"
              :label="item.ht_rh_Name"
              :value="item.ht_rh_ID">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addDialog = false"> </el-button>
        <el-button type="primary" @click="addSubmit"> </el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="updateDialog">
      <el-form :model="updateHotelRoomRoomFacilitiesObj">

        <el-form-item label=":" :label-width="formLabelWidth">
          <el-select v-model="RoomFacilitiesTypeID" placeholder="" @change="changeRoomFacilities">
            <el-option
              v-for="item in hotelRoomFacilitiesTypeList"
              :key="item.ht_rht_ID"
              :label="item.ht_rht_Name"
              :value="item.ht_rht_ID">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-select v-model="updateHotelRoomRoomFacilitiesObj.ht_rth_RoomHardID" placeholder="" >
            <el-option
              v-for="item in roomFacilitiesList"
              :key="item.ht_rh_ID"
              :label="item.ht_rh_Name"
              :value="item.ht_rh_ID">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="updateDialog = false"> </el-button>
        <el-button type="primary" @click="updateSubmit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default{
    name: '',
    data(){
      return {
        roomID:'',
        total: 0,
        isLoading: false,
        addDialog: false,
        updateDialog: false,
        formLabelWidth: '120px',
        RoomFacilitiesTypeID:'',
        addOptions:{
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "ht_rth_RoomHardID": "",//
            "ht_rth_RoomID": "",//ID
            "ht_ht_hotelID": "",//ID
          }
        }
      }
    },
    computed: mapGetters([
      'hotelRoomRoomFacilitiesList',
      'hotelRoomFacilitiesTypeList',
      'roomFacilitiesList',
      'hotelID',
      'updateHotelRoomRoomFacilitiesObj'
    ]),
    created(){
      let id = this.$route.params.id;
      if (!id) {
        this.$notify({
          message: '!',
          position: 'top-left',
          type: 'error'
        });
        this.$router.push({name: 'HotelRoom'});
        return
      }
      this.roomID = id;
      this.initData(1)
    },
    methods: {
      //
      changeRoomFacilities(){
        //
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "lb",
          "pcName": "",
          "ht_rh_ID": "",//
          "ht_rh_Name": "",//
          "ht_rh_RoomHardTypeID": this.RoomFacilitiesTypeID,//ID
          "ht_rh_IsHot": "",//
        }
        this.$store.dispatch('initRoomFacilities',options)
      },
      //
      handleCurrentChange(num){
        this.initData(num);
      },
      initData(page){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "ht_rth_ID": "",//ID
          "ht_rth_RoomHardID": "",//
          "ht_rth_RoomID": this.roomID,//ID
          "ht_ht_hotelID": "",//ID
          "page":page?page:1,//
          "rows":"5",//
        };
        this.isLoading = true;
        this.$store.dispatch('initHotelRoomRoomFacilities', options)
        .then((total) => {
          this.total = total;
          this.isLoading = false
        }, err => {
          this.$notify({
            message: err,
            type: 'error'
          });
        })
      },
      //
      search(){

      },
      Add(){
        this.$store.commit('setTranstionFalse');
        this.addDialog = true;
      },
      //
      addSubmit(){
        this.addOptions.data.ht_ht_hotelID = this.hotelID;
        this.addOptions.data.ht_rth_RoomID = this.roomID;
        this.$store.dispatch('AddHotelRoomRoomFacilities',this.addOptions)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(1)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          });
        this.addDialog = false;
      },
      Update(id){
        this.$store.commit('setTranstionFalse');
        this.updateDialog = true;
        this.$store.commit('UpdateHotelRoomRoomFacilities', id)
      },
      //
      updateSubmit(){
        let updateOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.updateHotelRoomRoomFacilitiesObj
        };
        this.$store.dispatch('UpdateHotelRoomRoomFacilities',updateOptions)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData( 1)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          });
        this.updateDialog = false;
      },
      //
      Delete(id){
        let deleteOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "lb",
          "pcName": "",
          "data": {
            "ht_rth_ID": id//ID
          }
        };
        this.$store.dispatch('DeleteHotelRoomRoomFacilities',deleteOptions)
        .then(suc => {
          this.$notify({
            message: suc,
            type: 'success'
          });
          this.initData(1)
        }, err => {
          this.$notify({
            message: err,
            type: 'error'
          });
        });
      },
    },
  }
</script>
<style scoped>

</style>



