<template>
  <div>
    <div id="wrap" class="clearfix">
      <h1 class="userClass">_</h1>

      <!---->

      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-select v-model="scenicNumber" placeholder="" @change="changeScenicNumber">
            <el-option
              v-for="item in ticketAttractionsList"
              :key="item.tm_ts_Code"
              :label="item.tm_ts_Name"
              :value="item.tm_ts_Code">
            </el-option>
          </el-select>
          <el-select v-model="ticketTypeNumber" placeholder="">
            <el-option
              v-for="item in ticketTypeList"
              :key="item.tm_tt_ID"
              :label="item.tm_tt_Name"
              :value="item.tm_tt_ID">
            </el-option>
          </el-select>
          <el-form-item>
            <div class="block">
              <el-date-picker
                v-model="searchTime"
                type="date"
                placeholder="()"
                value-format="yyyy-MM-dd"
              >
              </el-date-picker>
            </div>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search"></el-button>
            <el-button type="primary" @click="Add"></el-button>
            <el-button type="danger" @click="deleteBtn"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->

      <el-table
        :data="ticketTypeTicketPriceList"
        style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="">
                <span>{{ props.row.tm_tp_ID }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_tt_ID }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_tp_TicketPrice }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_tp_SalePrice }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_tp_RealPrice }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_tp_BackPriceFee }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_tp_Date }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_tp_Limit }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_tp_Remark }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="tm_tp_RealPrice">
        </el-table-column>
        <el-table-column
          label=""
          prop="tm_tp_Limit">
        </el-table-column>
        <el-table-column
          label="()"
          prop="tm_tp_BackPriceFee">
        </el-table-column>
        <el-table-column
          label=""
          prop="tm_tp_Date">
        </el-table-column>
      </el-table>

      <!---->

      <div class="block" style="float: right;">
        <el-pagination
          @current-change="handleCurrentChange"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>

      <!---->

      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="addOptions.tm_tt_ID" placeholder="">
              <el-option
                v-for="item in ticketTypeList"
                :key="item.tm_tt_ID"
                :label="item.tm_tt_Name"
                :value="item.tm_tt_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.tm_tp_Limit"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <div class="block">
              <el-date-picker
                v-model="addOptions.createFrom"
                type="date"
                placeholder=""
                value-format="yyyy-MM-dd"
              >
              </el-date-picker>
            </div>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <div class="block">
              <el-date-picker
                v-model="addOptions.createTo"
                type="date"
                placeholder=""
                value-format="yyyy-MM-dd"
              >
              </el-date-picker>
            </div>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->

      <el-dialog title="" :visible.sync="deleteDialog">
        <el-form :model="deleteOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="deleteOptions.tm_tt_ID" placeholder="">
              <el-option
                v-for="item in ticketTypeList"
                :key="item.tm_tt_ID"
                :label="item.tm_tt_Name"
                :value="item.tm_tt_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <div class="block">
              <el-date-picker
                v-model="deleteOptions.dateFrom"
                type="date"
                placeholder=""
                value-format="yyyy-MM-dd"
              >
              </el-date-picker>
            </div>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <div class="block">
              <el-date-picker
                v-model="deleteOptions.dateTo"
                type="date"
                placeholder=""
                value-format="yyyy-MM-dd"
              >
              </el-date-picker>
            </div>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="deleteDialog = false"> </el-button>
          <el-button type="primary" @click="deleteSubmit"> </el-button>
        </div>
      </el-dialog>

    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([
      'ticketAttractionsList',
      'ticketTypeList',
      'ticketTypeTicketPriceList'
    ]),
    data() {
      return {
        searchTime: '',
        loginId: '',
        scenicNumber: '',
        ticketTypeNumber: '',
        total: 0,
        addDialog: false,
        formLabelWidth: '120px',
        addOptions: {
          "tm_tt_ID": "",
          "tm_tp_Limit": "",
          "createFrom": "",
          "createTo": "",
        },
        deleteDialog: false,
        deleteOptions: {
          tm_tt_ID: '',
          dateFrom: '',
          dateTo: ''
        }
      }
    },
    methods: {
      //
      handleCurrentChange(num) {
        this.initData(this.ticketTypeNumber, num)
      },
      //
      initTicketAttraction() {
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "tsCode": "",    //
          "tsName": '',//
          "tradeInfoID": this.loginId.sm_ai_ID,//
          "isDelete": 0,//
          "page": 1,
          "rows": 5
        };
        this.$store.dispatch('initTicketAttractions', options)
      },
      //
      initTicketType(id) {
        let getTicketTypePriceList = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "tm_ts_Code": id ? id : '',
          "tm_tt_TradeInfoID": this.loginId.sm_ai_ID,
          "tm_tt_IsDelete": 0,
          "page": 1,
          "rows": 100
        };
        this.$store.dispatch('initTicketType', getTicketTypePriceList)
      },
      //
      changeScenicNumber() {
        this.initTicketType(this.scenicNumber)
      },
      //
      initData(id, num) {
        let getTicketTypePriceList = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "tm_tt_ID": id ? id : '',
          "dateFrom": this.searchTime ? this.searchTime : '',
          "page": num ? num : 1,
          "rows": 5
        };
        this.$store.dispatch('initTicketTypeTicketPrice', getTicketTypePriceList)
          .then(total => {
            this.total = total;
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      //
      search() {
        this.initData(this.ticketTypeNumber)
      },
      //
      Add() {
        this.$store.commit('setTranstionFalse');
        this.addDialog = true;
      },
      //
      addSubmit() {
        let insertTicketTypePrice = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.addOptions
        };
        this.$store.dispatch('addTicketTypeTicketPriceSubmit', insertTicketTypePrice)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.ticketTypeNumber);
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.addDialog = false;
      },
      //
      deleteBtn() {
        this.$store.commit('setTranstionFalse');
        this.deleteDialog = true;
      },
      //
      deleteSubmit() {
        let deleteTicketTypePrice = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "tm_tt_ID": this.deleteOptions.tm_tt_ID,
          "dateFrom": this.deleteOptions.dateFrom,
          "dateTo": this.deleteOptions.dateTo
        };
        this.$store.dispatch('deleteTicketTypeTicketPriceSubmit',deleteTicketTypePrice)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.ticketTypeNumber);
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.deleteDialog = false;
      },
    },
    created() {
      this.loginId = JSON.parse(sessionStorage.getItem('admin'));
      this.initTicketAttraction();
    }
  }
</script>
<style scoped>
</style>



