<script>
  import {mapGetters} from 'vuex'

  export default {
    name: '',
    data() {
      return {
        formLabelWidth: '120px',
        isMap: false,
        siteName: '',//
        adminUserInfo: {},
        total: 0,
        addDialog: false,//
        updateDialog: false,//
        form: {
          x: '',
          y: ''
        },
        //
        addOptions: {
          "tm_ts_Code": "",//()
          "tm_ts_Name": "",//
          "tm_ts_TradeInfoID": '',
          "tm_ts_ThemeTypeID": '',//
          "tm_ts_GreatID": "",//
          "tm_ts_CountrieID": "",//
          "tm_ts_ProviceID": "",//
          "tm_ts_CityID": "",//
          "tm_ts_ContryID": "",//
          "tm_ts_ShowImage": "",//
          "tm_ts_Introduce": "",//
          "tm_ts_Detailedintroduction": "",//
          "tm_ts_Address": "",//
          "tm_ts_Opentime": "",//
          "tm_ts_Time": "",//
          "tm_ts_Phone": "",//
          "tm_ts_Longitude": "",//
          "tm_ts_Latitude": "",//
          "tm_ts_IsHot": "",//01
          "tm_ts_IsChoice": "",//01
          "tm_ts_IsOversea": "",//01
          "tm_ts_IsSeasonChoice": "",// 01
          "tm_ts_SuggestPrice": "",//
          "tm_ts_Remark": "",//
        },
        //
        selectedScenicSpot: [
          {
            value: '0',
            label: ''
          },
          {
            value: '1',
            label: ''
          }
        ],
        //
        overseasScenicSpots: [
          {
            value: '0',
            label: ''
          },
          {
            value: '1',
            label: ''
          }
        ],
        //
        seasonSelection: [
          {
            value: '0',
            label: ''
          },
          {
            value: '1',
            label: ''
          }
        ],
        //
        selectDelete: [
          {
            value: '0',
            label: ''
          },
          {
            value: '1',
            label: ''
          }
        ],
        ImageURL: [],
        showImageList: [],
        updateDialog: false,
        isLoading: false,
        updateImageURL: [],
        value: '',
      }
    },
    computed: mapGetters([
      'ticketAttractionsList',
      'themeTypeList',
      'ticketGreatList',
      'ticketCountrieList',
      'ticketProviceList',
      'ticketCityList',
      'ticketContryList',
      'updateTicketAttractionsObj'
    ]),
    created() {
      this.adminUserInfo = JSON.parse(sessionStorage.getItem('admin'));
      this.initData('', 1);
      this.initGreat();
    },
    mounted() {
      var _this = this;
      var geolocation = new BMap.Geolocation();
      geolocation.getCurrentPosition(function (r) {
        _this.form.x = r.point.lng;
        _this.form.y = r.point.lat;
        if (this.getStatus() == BMAP_STATUS_SUCCESS) {
          var map = new BMap.Map("allmap");
          // var point = new BMap.Point(116.400244, 39.92556);
          map.centerAndZoom(r.point, 12); //
          map.enableScrollWheelZoom(true); //
          var marker = new BMap.Marker(r.point);// 

          map.addOverlay(marker);  // 
          map.panTo(r.point);
          // alert('' + r.point.lng + ',' + r.point.lat);

          marker.enableDragging(); //
          //marker.disableDragging();           // 

          // 
          marker.addEventListener("dragend", function (e) {

            var x = e.point.lng; //
            var y = e.point.lat; //
//            alert("" + x + "" + y);
            _this.form.x = x;
            _this.form.y = y;
          });
        }
        else {
          alert('failed' + this.getStatus());
        }
      }, {enableHighAccuracy: true})
    },
    methods: {
      clickGo(id) {
        if (this.value == 0) {
          this.goPredeterminedInstructions(id);
        }
        if (this.value == 1) {
          this.goTrafficInformation(id);
        }
      },
      //
      uploadImg(file) {
        return new Promise(function (relove, reject) {
          lrz(file)
            .then(data => {
              relove(data.base64.split(',')[1])
            })
        })
      },
      //
      uploaNode() {
        setTimeout(() => {
          if (this.$refs.upload) {
            this.$refs.upload.addEventListener('change', data => {
              for (var i = 0; i < this.$refs.upload.files.length; i++) {
                this.uploadImg(this.$refs.upload.files[i]).then(data => {
                  this.$store.dispatch('uploadAdminImgs', {
                    imageData: data
                  })
                    .then(data => {
                      if (data) {
                        this.ImageURL.push(data.data);
                      } else {
                        this.$notify({
                          message: '!',
                          type: 'error'
                        });
                      }
                    })
                })
              }
            })
          }
          ;
          if (this.$refs.updateUpload) {
            this.$refs.updateUpload.addEventListener('change', data => {
              for (var i = 0; i < this.$refs.updateUpload.files.length; i++) {
                this.uploadImg(this.$refs.updateUpload.files[i]).then(data => {
                  this.$store.dispatch('uploadAdminImgs', {
                    imageData: data
                  })
                    .then(data => {
                      if (data) {
                        this.updateImageURL.push(data.data);
                      } else {
                        this.$notify({
                          message: '!',
                          type: 'error'
                        });
                      }
                    })
                })
              }
            })
          }
          ;
        }, 30)
      },
      //
      handleCurrentChange(num) {
        this.initData('', num)
      },
      initData(name, page) {
        this.isLoading = true;
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "tm_ts_Code": "",    //
          "tm_ts_Name": name ? name : '',//
          "tm_ts_TradeInfoID": this.adminUserInfo.sm_ai_ID,//
          "tm_ts_IsDelete": 0,//
          "tm_ts_IsPass": "",//(012)
          "tm_ts_ShowTop": "",//01
          "tm_ts_IsHot": "",//01)
          "tm_ts_ThemeTypeID": "",//
          "page": page ? page : 1,
          "rows": 5
        };
        this.$store.dispatch('initTicketAttractions', options)
          .then(total => {
            this.total = total;
            this.isLoading = false;
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      //
      search() {
        this.initData(this.siteName, 1)
      },

      initGreat() {
        let getAreaProvice = {
          "areaPid": -1
        }
        this.$store.dispatch('initTicketGreat', getAreaProvice)
          .then(() => {
          })
      },
      //
      changeGreat() {
        this.changeCountrie();
      },

      //
      changeCountrie() {
        let getAreaProvice = {
          "areaPid": this.addOptions.tm_ts_GreatID
        }
        this.$store.dispatch('initTicketCountrie', getAreaProvice)
          .then()
        this.changeProvice();
      },
      //
      changeProvice() {
        let getAreaProvice = {
          "areaPid": this.addOptions.tm_ts_CountrieID
        }
        this.$store.dispatch('initTicketProvice', getAreaProvice)
          .then()
        this.changeCity();
      },
//      
      changeCity() {
        let getAreaProvice = {
          "areaPid": this.addOptions.tm_ts_ProviceID
        }
        this.$store.dispatch('initTicketCity', getAreaProvice)
          .then()
        this.changeContry();
      },
//      
      changeContry() {
        let getAreaProvice = {
          "areaPid": this.addOptions.tm_ts_CityID
        }
        this.$store.dispatch('initTicketContry', getAreaProvice)
          .then()
      },
      //
      Add() {
        this.addOptions.tm_ts_GreatID = '';
        this.addOptions.tm_ts_CountrieID = '';
        this.addOptions.tm_ts_ProviceID = '';
        this.addOptions.tm_ts_CityID = '';
        this.addOptions.tm_ts_ContryID = '';
        this.$store.commit('setTranstionFalse');
        this.addDialog = true;
        this.uploaNode();
        this.addOptions.tm_ts_TradeInfoID = this.adminUserInfo.sm_ai_ID;
      },

      //
      addSubmit() {
        this.addOptions.tm_ts_ShowImage = this.ImageURL.join(',');
        console.log(this.addOptions.tm_ts_ShowImage)
        let insertTourSite = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "data": this.addOptions
        }
        this.$store.dispatch('addTicletInformation', insertTourSite)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData();
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.addDialog = false;
      },

//      
      update(id) {
        this.addOptions.tm_ts_GreatID = '';
        this.addOptions.tm_ts_CountrieID = '';
        this.addOptions.tm_ts_ProviceID = '';
        this.addOptions.tm_ts_CityID = '';
        this.addOptions.tm_ts_ContryID = '';
        this.$store.commit('setTranstionFalse');
        this.updateDialog = true;
        this.uploaNode();
        this.$store.commit('updateTicketAttractions', id);
        this.updateTicketAttractionsObj.tm_ts_IsHot = '';
        this.updateTicketAttractionsObj.tm_ts_IsOversea = '';
        this.updateTicketAttractionsObj.tm_ts_IsSeasonChoice = '';
        this.updateTicketAttractionsObj.tm_ts_ThemeTypeID = '';
      },

      //
      updateSubmit() {
        if (this.updateImageURL != '') {
          this.updateTicketAttractionsObj.tm_ts_ShowImage = this.updateImageURL.join(',');
        } else {
          this.updateTicketAttractionsObj.tm_ts_ShowImage = this.updateTicketAttractionsObj.tm_ts_ShowImage.join(',');
        }
        let updateTourSite = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "data": this.updateTicketAttractionsObj
        };
        this.$store.dispatch('updateTicketAttractionsSubmit', updateTourSite)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData('', 1);
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.updateDialog = false;
      },

      //
      deleteTicketAttractions(id) {
        let deleteTourSite = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "data": {
            "tm_ts_Code": id,
          }
        };
        this.$store.dispatch('deleteTicketAttractions', deleteTourSite)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData('', 1);
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },

      //

      goPredeterminedInstructions(id) {
        this.$router.push({name: 'PredeterminedInstructions', params: {id: id}})
      },

      //

      goTrafficInformation(id) {
        this.$router.push({name: 'TrafficInformation', params: {id: id}})
      }
    },
  }
</script>
<style scoped>
  #l-map {
    height: 500px;
    width: 100%;
  }
</style>



