<template>
  <div>
    <div id="wrap" class="clearfix">
      <h1 class="userClass"></h1>
      <!---->
      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-select v-model="storeId" placeholder="" @change="changeRoom">
              <el-option
                v-for="item in foodStoreInformtionList"
                :key="item.fd_sf_ID"
                :label="item.fd_sf_ProductName"
                :value="item.fd_sf_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select v-model="roomId" placeholder="">
              <el-option
                v-for="item in foodStoreRoomList"
                :key="item.fd_sfr_ID"
                :label="item.fd_sfr_RoomName"
                :value="item.fd_sfr_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search"></el-button>
            <el-button type="primary" @click="add"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->

      <el-table
        :data="foodRoomPictureList"
        style="width: 100%">

        <el-table-column
          prop="fd_sfr_RoomName"
          label=""
          align="center">
        </el-table-column>

        <el-table-column
          prop="fd_ri_Remark"
          label=""
          align="center">
        </el-table-column>

        <el-table-column
          label=""
          align="center">
          <template slot-scope="scope">
            <img v-lazy="scope.row.fd_ri_Image" width="128" height="80"
                 @click="displayBigPicture(scope.row.fd_ri_Image)">
          </template>
        </el-table-column>

        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="update(scope.row)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="Delete(scope.row.fd_ri_ID)">
            </el-button>
          </template>
        </el-table-column>

      </el-table>

      <!---->

      <el-dialog
        title=""
        :visible.sync="bigPictureDialog"
        width="50%">
        <img v-lazy="imgUrl" width="100%">
        <span slot="footer" class="dialog-footer">
          <el-button @click="bigPictureDialog = false"> </el-button>
        </span>
      </el-dialog>

      <!---->

      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="addOptions.fd_ri_RoomID" placeholder="">
              <el-option
                v-for="item in foodStoreRoomList"
                :key="item.fd_sfr_ID"
                :label="item.fd_sfr_RoomName"
                :value="item.fd_sfr_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <a href="javascript:;" class="file">
              <input type="file" name="" ref="upload" accept="image/*">
            </a>
            <img v-lazy="addOptions.fd_ri_Image" v-show="addOptions.fd_ri_Image" width="128" height="80">
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.fd_ri_Remark"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->

      <el-dialog title="" :visible.sync="updateDialog">
        <el-form :model="updateObj">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="updateObj.fd_ri_RoomID" placeholder="">
              <el-option
                v-for="item in foodStoreRoomList"
                :key="item.fd_sfr_ID"
                :label="item.fd_sfr_RoomName"
                :value="item.fd_sfr_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <a href="javascript:;" class="file">
              <input type="file" name="" ref="upload" accept="image/*">
            </a>
            <p></p>
            <img v-lazy="addOptions.fd_ri_Image" v-show="addOptions.fd_ri_Image" width="128" height="80">
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateObj.fd_ri_Remark"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateDialog = false"> </el-button>
          <el-button type="primary" @click="updateSubmit"> </el-button>
        </div>
      </el-dialog>


    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([
      'foodStoreInformtionList',
      'foodStoreRoomList',
      'foodRoomPictureList'
    ]),
    data() {
      return {
        storeId: '',
        roomId: '',
        formLabelWidth: '120px',
        total: 0,
        addOptions: {
          "fd_ri_RoomID": "",//
          "fd_ri_Image": "",//
          "fd_ri_Remark": "",//
        },
        addDialog: false,
        updateDialog: false,
        updateObj: {},
        bigPictureDialog: false,
        imgUrl: '',
      }
    },
    methods: {
      //
      uploadImg(file) {
        return new Promise(function (relove, reject) {
          lrz(file)
            .then(data => {
              relove(data.base64.split(',')[1])
            })
        })
      },

      uploaNode() {
        this.addOptions.fd_ri_Image = '';
        setTimeout(() => {
          if (this.$refs.upload) {
            this.$refs.upload.addEventListener('change', data => {
              for (var i = 0; i < this.$refs.upload.files.length; i++) {
                this.uploadImg(this.$refs.upload.files[i]).then(data => {
                  this.$store.dispatch('foodUploadAdminImgs', {
                    imageData: data
                  })
                    .then(data => {
                      if (data) {
                        this.addOptions.fd_ri_Image = data.data;
                      } else {
                        this.$notify({
                          message: '!',
                          type: 'error'
                        });
                      }
                    })
                })
              }
            })
          }
        }, 30)
      },
      //
      changeRoom(id) {
        this.initRoomData(id)
      },
      //
      initRoomData(id) {
        if (!id) {
          this.$notify({
            message: '',
            type: 'error'
          })
          return;
        }
        let initStoreRoom = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "fd_sfr_ID": "",//
          "fd_sfr_StoreFrontID": id,//
          "fd_sfr_RoomName": "",//
          "page": "1",
          "rows": "10000",
        }
        this.$store.dispatch('initFoodStoreRoom', initStoreRoom).then(
          total => {
            this.total = total;
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },

      //
      initData(id) {
        if (!id) {
          this.$notify({
            message: '',
            type: 'error'
          })
          return;
        }
        let selectRoomImageInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "page": "1",
          "rows": "10",
          "fd_ri_ID": "",//
          "fd_ri_RoomID": id,//
        }
        this.$store.dispatch('initFoodRoomPicture', selectRoomImageInfo)
          .then(total => {
            this.total = total;
          })
      },
      //
      search() {
        this.initData(this.roomId)
      },
      //
      displayBigPicture(urlData) {
        this.$store.commit('setTranstionFalse');
        this.imgUrl = urlData
        this.bigPictureDialog = true;
      },
      //
      add() {
        this.$store.commit('setTranstionFalse');
        this.addDialog = true;
        this.uploaNode();
      },
      //
      addSubmit() {
        let insertRoomImageInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.addOptions
        };
        this.$store.dispatch('addFoodRoomPicture', insertRoomImageInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            })
            this.initData(this.roomId)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            })
          })
        this.addDialog = false;
      },
      //
      update(rowData) {
        this.updateObj = rowData;
        this.$store.commit('setTranstionFalse');
        this.updateDialog = true;
        this.uploaNode();
      },
      //
      updateSubmit() {
        if (this.addOptions.fd_ri_Image) {
          this.updateObj.fd_ri_Image = this.addOptions.fd_ri_Image
        }
        let updateRoomImageInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.updateObj
        };
        this.$store.dispatch('updateFoodRoomPicture', updateRoomImageInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            })
            this.initData(this.roomId)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            })
          })
        this.updateDialog = false;
      },
      //
      Delete(id) {
        let deleteRoomImageInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "fd_ri_ID": id ? id : '',//
          }

        };
        this.$store.dispatch('deleteFoodRoomPicture', deleteRoomImageInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            })
            this.initData(this.roomId)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            })
          })
      }


    },
  }
</script>
<style scoped>
</style>



