<template>
  <div>
    <div id="wrap" class="clearfix">
      <h1 class="userClass"></h1>

      <!---->

      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-input v-model="siteId"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search"></el-button>
            <el-button type="primary" @click="Add"></el-button>
          </el-form-item>
        </el-form>
      </el-col>


      <!---->

      <el-table
        :data="ticketTypeList"
        style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="">
                <span>{{ props.row.tm_tt_ID }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_ts_Name}}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_tt_Name }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_tt_TradeInfoID }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_tt_HelpCode }}</span>
              </el-form-item>
              <el-form-item label="">
                <img v-for="item,index in props.row.tm_tt_Image" :src="item" alt="" :key="index" width="300"
                     height="150">
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_tt_Description }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_tt_ExpireDay }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_tt_BeforeTime }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_tt_TicketPrice }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_tt_RealPrice }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_tt_Remark }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="tm_tt_ID">
        </el-table-column>
        <el-table-column
          label=""
          prop="tm_tt_Name">
        </el-table-column>
        <el-table-column
          label=""
          prop="tm_ts_Name">
        </el-table-column>
        <el-table-column
          label=""
          prop="tm_tt_RealPrice">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="update(scope.row.tm_tt_ID)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="deleteTicketType(scope.row.tm_tt_ID)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->

      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.tm_tt_Name"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="addOptions.tm_ts_Code" placeholder="">
              <el-option
                v-for="item in ticketAttractionsList"
                :key="item.tm_ts_Code"
                :label="item.tm_ts_Name"
                :value="item.tm_ts_Code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.tm_tt_HelpCode"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <a href="javascript:;" class="file">
              <input type="file" name="" ref="upload" accept="image/*" multiple>
            </a>
            <img
              style="display: block"
              v-for="item in ImageURL"
              :src="item"
              width="280"
              height="125"
              v-show="ImageURL.length"
            >
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.tm_tt_Description" type="textarea"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.tm_tt_ExpireDay" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.tm_tt_BeforeTime" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.tm_tt_Remark" type="textarea"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.tm_tt_TicketPrice" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.tm_tt_RealPrice" placeholder=""></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->

      <el-dialog title="" :visible.sync="updateDialog">
        <el-form :model="updateTicketTypeObj">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateTicketTypeObj.tm_tt_Name"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="updateTicketTypeObj.tm_ts_Code" placeholder="">
              <el-option
                v-for="item in ticketAttractionsList"
                :key="item.tm_ts_Code"
                :label="item.tm_ts_Name"
                :value="item.tm_ts_Code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateTicketTypeObj.tm_tt_HelpCode"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <a href="javascript:;" class="file">
              <input type="file" name="" ref="upload" accept="image/*" multiple>
            </a>
            <img
              v-if="updateTicketTypeObj.tm_tt_Image.length&&!ImageURL.length"
              style="display: block"
              v-for="item,index in updateTicketTypeObj.tm_tt_Image"
              :src="item"
              width="280"
              height="125"
            >
            <img
              style="display: block"
              v-for="item,index in ImageURL"
              :src="item"
              width="280"
              height="125"
              v-show="ImageURL.length"
            >
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateTicketTypeObj.tm_tt_Description" type="textarea"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateTicketTypeObj.tm_tt_ExpireDay" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateTicketTypeObj.tm_tt_BeforeTime" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateTicketTypeObj.tm_tt_Remark" type="textarea"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateTicketTypeObj.tm_tt_TicketPrice" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateTicketTypeObj.tm_tt_RealPrice" placeholder=""></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateDialog = false"> </el-button>
          <el-button type="primary" @click="updateSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->
      <div class="block" style="float: right;">
        <el-pagination
          @current-change="handleCurrentChange"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>

    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([
      'ticketTypeList',
      'ticketAttractionsList',
      'updateTicketTypeObj'
    ]),
    data() {
      return {
        loginId: '',
        siteId: '',
        total: 0,
        addDialog: false,
        formLabelWidth: '120px',
        addOptions: {
          "tm_ts_Code": "",//
          "tm_tt_Name": "",//
          "tm_tt_TradeInfoID": "",//
          "tm_tt_HelpCode": "",//
          "tm_tt_Image": "",//
          "tm_tt_Description": "",//
          "tm_tt_ExpireDay": "",//
          "tm_tt_BeforeTime": "",//()
          "tm_tt_Remark": "",//()
          "tm_tt_TicketPrice": "",//
          "tm_tt_RealPrice": "",//
        },
        ImageURL: [],
        updateDialog: false,
      }
    },
    methods: {

      //
      uploadImg(file) {
        return new Promise(function (relove, reject) {
          lrz(file)
            .then(data => {
              relove(data.base64.split(',')[1])
            })
        })
      },
      //
      uploaNode() {
        setTimeout(() => {
          if (this.$refs.upload) {
            this.$refs.upload.addEventListener('change', data => {
              for (var i = 0; i < this.$refs.upload.files.length; i++) {
                this.uploadImg(this.$refs.upload.files[i]).then(data => {
                  this.$store.dispatch('uploadAdminImgs', {
                    imageData: data
                  })
                    .then(data => {
                      if (data) {
                        this.ImageURL.push(data.data);
                      } else {
                        this.$notify({
                          message: '!',
                          type: 'error'
                        });
                      }
                    })
                })
              }
            })
          }

        }, 30)
      },
      //
      initTicketAttraction() {
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "tm_ts_Code": "",    //
          "tm_ts_Name": "",//
          "tm_ts_TradeInfoID": this.loginId.sm_ai_ID,//
          "tm_ts_IsDelete": 0,//
          "tm_ts_IsPass": "",//(012)
          "tm_ts_ShowTop": "",//01
          "tm_ts_IsHot": "",//01)
          "tm_ts_ThemeTypeID": "",//
          "page": 1,
          "rows": 5
        };
        this.$store.dispatch('initTicketAttractions', options)

      },
      //
      handleCurrentChange(num) {
        this.initData('', num)
      },
      //
      initData(id, num) {
        let getTicketTypePriceList = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "tm_ts_Code": id ? id : '',
          "tm_tt_TradeInfoID": this.loginId.sm_ai_ID,
          "tm_tt_IsDelete": 0,
          "page": num ? num : 1,
          "rows": 5
        };
        this.$store.dispatch('initTicketType', getTicketTypePriceList)
          .then(total => {
            this.total = total;
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      //
      search() {
        this.initData(this.siteId)
      },
      // 
      Add() {
        this.ImageURL = [];
        this.$store.commit('setTranstionFalse');
        this.addDialog = true;
        this.uploaNode();
      },
      //
      addSubmit() {
        this.addOptions.tm_tt_TradeInfoID = this.loginId.sm_ai_ID;
        this.addOptions.tm_tt_Image = this.ImageURL.join(',');
        let insertTicketType = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "data": this.addOptions
        };
        this.$store.dispatch('addTicketTypeSubmit', insertTicketType)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData();
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.addDialog = false;
      },
      //
      update(id) {
        this.ImageURL = [];
        this.$store.commit('setTranstionFalse');
        this.$store.commit('updateTicketType', id);
        this.updateDialog = true;
        this.uploaNode();
      },

      //
      updateSubmit() {
        if (this.ImageURL.length) {
          this.updateTicketTypeObj.tm_tt_Image = this.ImageURL.join(',');
        } else {
          this.updateTicketTypeObj.tm_tt_Image = this.updateTicketTypeObj.tm_tt_Image.join(',');
        }
        let updateTicketType = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "data": this.updateTicketTypeObj
        }
        this.$store.dispatch('updateTicketTypeSubmit', updateTicketType)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData();
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.updateDialog = false;

      },

      //
      deleteTicketType(id) {
        let deleteTicketType = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "data": {
            "tm_tt_ID": id,
          }

        };
        this.$store.dispatch('deleteTicketType', deleteTicketType)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData();
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      }
    },

    created() {
      this.loginId = JSON.parse(sessionStorage.getItem('admin'));
      this.initTicketAttraction();
      this.initData();
    }
  }
</script>
<style scoped>
</style>



