<template>
  <div>
    <div id="wrap" class="clearfix">
      <h1 class="userClass"></h1>

      <!---->
      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-select v-model="searchId" placeholder="">
              <el-option
                v-for="item in searchArr"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->

      <el-table
        :data="ticketQueryOrderList"
        style="width: 100%; margin-top: 20px;">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label=" ID">
                <span>{{ props.row.tm_or_OrderID }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_TourSiteCode }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_TicketTypeID }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_TradeInfoID }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_TradeInfoName }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_Password }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_TicketCount }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_SumPrice }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_SumService }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_OutStatus }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_UseState }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_FailID }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_FailContent }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_SellID }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_SellName }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_FullPrice }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_ChildPrice }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_CreateTime }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_PayTime }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_PayWay }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_PayParam }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_PayState }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_IsBalance }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_UseTime }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_UserID }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_Remark }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_Email }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_TelePhone }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_GoodsListName }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_ConnectName }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.tm_or_TravelDate }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="tm_or_ConnectName">
        </el-table-column>
        <el-table-column
          label=""
          prop="tm_or_TelePhone">
        </el-table-column>
        <el-table-column
          label=""
          prop="tm_or_TravelDate">
        </el-table-column>
        <el-table-column
          label=""
          prop="tm_or_OutStatus">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="success" size="mini" @click="confirmOrder(scope.row.tm_or_OrderID)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->

      <div class="block" style="text-align: right">
        <el-pagination
          @current-change="handleCurrentChange"
          :page-size="5"
          layout="prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>

    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([
      'ticketQueryOrderList'
    ]),
    data() {
      return {
        total: 0,
        searchId: '',
        searchArr: [
          {
            value: 0,
            label: ''
          },
          {
            value: 1,
            label: ''
          }
        ],
        userInfo:'',
      }
    },
    methods: {
      //
      handleCurrentChange(num) {
        this.initData(num)
      },
      //
      initData(id, num) {
        let getTradeOrderInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "tm_or_UserID": "", //
          "tm_or_TradeInfoID": this.userInfo.sm_ai_ID, //
          "tm_or_OrderID": "", //
          "tm_or_PayState": id ? id : '', //(01)
          "tm_or_IsBalance": "", //(01)
          "tm_or_OutStatus": "", //(012)
          "tm_or_UseState": "", //(012)
          "page": num ? num : 1,
          "rows": 10
        };
        this.$store.dispatch('initTicketQueryOrder', getTradeOrderInfo)
          .then(total => {
            this.total = total;
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      search(){
        this.initData(this.searchId)
      },
      confirmOrder(id) {
        let reSureOrder = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "orderID": id ? id : ''
        };
        this.$store.dispatch('ticketConfirmOrder', reSureOrder)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData()
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      }
    },
    created() {
      this.userInfo = JSON.parse(sessionStorage.getItem('admin'))
      this.initData();
    }
  }
</script>
<style scoped>
</style>



