<template>
  <div>
    <div id="wrap" class="clearfix">
      <h1 class="userClass"></h1>

      <!---->
      <div class="search">
        <el-select v-model="ticketAttractionsValue" placeholder="">
          <el-option
            v-for="item in ticketAttractionsList"
            :key="item.tm_ts_Code"
            :label="item.tm_ts_Name"
            :value="item.tm_ts_Code">
          </el-option>
        </el-select>

        <el-button type="primary" @click="search"></el-button>
        <el-button type="primary" @click="Add"></el-button>

      </div>
      <!---->

      <el-table
        :data="predeterminedInstructionsList"
        v-loading="isLoading"
        style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label=":">
                <span>{{ props.row.tm_bk_ID }}</span>
              </el-form-item>
              <el-form-item label=":">
                <el-popover
                  ref="popover1"
                  placement="top-start"
                  title=""
                  width="500"
                  trigger="hover"
                  :content="props.row.tm_bk_OpenTime">
                </el-popover>
                <el-button v-popover:popover1 size="mini"></el-button>
              </el-form-item>
              <el-form-item label=":">
                <el-popover
                  ref="popover1"
                  placement="top-start"
                  title=""
                  width="500"
                  trigger="hover"
                  :content="props.row.tm_bk_SpecialPolicy">
                </el-popover>
                <el-button v-popover:popover1 size="mini"></el-button>
              </el-form-item>
              <el-form-item label=":">
                <el-popover
                  ref="popover1"
                  placement="top-start"
                  title=""
                  width="500"
                  trigger="hover"
                  :content="props.row.tm_bk_Remind">
                </el-popover>
                <el-button v-popover:popover1 size="mini"></el-button>
              </el-form-item>
              <el-form-item label=":">
                <el-popover
                  ref="popover1"
                  placement="top-start"
                  title=""
                  width="500"
                  trigger="hover"
                  :content="props.row.tm_bk_HappyNotice">
                </el-popover>
                <el-button v-popover:popover1 size="mini"></el-button>
              </el-form-item>
              <el-form-item label=":">
                <el-popover
                  ref="popover1"
                  placement="top-start"
                  title=""
                  width="500"
                  trigger="hover"
                  :content="props.row.tm_bk_TicketSay">
                </el-popover>
                <el-button v-popover:popover1 size="mini"></el-button>
              </el-form-item>
              <el-form-item label=":">
                <el-popover
                  ref="popover1"
                  placement="top-start"
                  title=""
                  width="500"
                  trigger="hover"
                  :content="props.row.tm_bk_ReturnRule">
                </el-popover>
                <el-button v-popover:popover1 size="mini"></el-button>
              </el-form-item>
              <el-form-item label=":">
                <el-popover
                  ref="popover1"
                  placement="top-start"
                  title=""
                  width="500"
                  trigger="hover"
                  :content="props.row.tm_bk_SpecialMessage">
                </el-popover>
                <el-button v-popover:popover1 size="mini"></el-button>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="tm_bk_ID">
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <el-popover
              ref="popover1"
              placement="top-start"
              title=""
              width="500"
              trigger="hover"
              :content="scope.row.tm_bk_OpenTime">
            </el-popover>
            <el-button v-popover:popover1 size="mini"></el-button>
          </template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="update(scope.row.tm_bk_ID)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->

      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.tm_bk_OpenTime" type="textarea"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.tm_bk_SpecialPolicy" type="textarea"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.tm_bk_Remind" type="textarea"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.tm_bk_HappyNotice" type="textarea"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.tm_bk_TicketSay" type="textarea"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.tm_bk_ReturnRule" type="textarea"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.tm_bk_SpecialMessage" type="textarea"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->

      <el-dialog title="" :visible.sync="updateDialog">
        <el-form :model="updatePredeterminedInstructionsObj">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updatePredeterminedInstructionsObj.tm_bk_OpenTime" type="textarea"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updatePredeterminedInstructionsObj.tm_bk_SpecialPolicy" type="textarea"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updatePredeterminedInstructionsObj.tm_bk_Remind" type="textarea"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updatePredeterminedInstructionsObj.tm_bk_HappyNotice" type="textarea"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updatePredeterminedInstructionsObj.tm_bk_TicketSay" type="textarea"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updatePredeterminedInstructionsObj.tm_bk_ReturnRule" type="textarea"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updatePredeterminedInstructionsObj.tm_bk_SpecialMessage" type="textarea"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateDialog = false"> </el-button>
          <el-button type="primary" @click="updateSubmit"> </el-button>
        </div>
      </el-dialog>

    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([
      'predeterminedInstructionsList',
      'updatePredeterminedInstructionsObj',
      'ticketAttractionsList'
    ]),
    data() {
      return {
        siteName: '',
        isLoading: false,
        addOptions: {
          "tm_bk_TourSiteID": "",
          "tm_bk_OpenTime": "",
          "tm_bk_SpecialPolicy": "",
          "tm_bk_Remind": "",
          "tm_bk_HappyNotice": "",
          "tm_bk_TicketSay": "",
          "tm_bk_ReturnRule": "",
          "tm_bk_SpecialMessage": ""
        },
        addDialog: false,
        formLabelWidth: '120px',
        updateDialog: false,
        adminUserInfo:'',
        ticketAttractionsValue:'',
      }
    },
    methods: {
      //
      initTicketAttraction() {
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "tm_ts_Code": "",    //
          "tm_ts_Name": "",//
          "tm_ts_TradeInfoID": this.adminUserInfo.sm_ai_ID,//
          "tm_ts_IsDelete": 0,//
          "tm_ts_IsPass": "",//(012)
          "tm_ts_ShowTop": "",//01
          "tm_ts_IsHot": "",//01)
          "tm_ts_ThemeTypeID": "",//
          "page": 1,
          "rows": 5
        };
        this.$store.dispatch('initTicketAttractions', options)
      },
      initData(id) {
        if( !id ){
          this.$notify({
            message: '!!',
            type: 'error'
          });
          return
        }
        this.isLoading = true;
        let getBookKnow = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "tm_bk_TourSiteID": id,
        };
        this.$store.dispatch('initPredeterminedInstructions', getBookKnow)
          .then(() => {
            this.isLoading = false;
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },

      search(){
        this.initData(this.ticketAttractionsValue)
      },

      Add() {
        this.$store.commit('setTranstionFalse');
        this.addDialog = true;
        this.addOptions.tm_bk_TourSiteID = this.ticketAttractionsValue;
      },

      addSubmit() {
        let insertBookKnow = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "data": this.addOptions
        };
        this.$store.dispatch('addPredeterminedInstructionsSubmit', insertBookKnow)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.ticketAttractionsValue)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.addDialog = false;
      },

      update(id) {
        this.$store.commit('setTranstionFalse');
        this.updateDialog = true;
        this.$store.commit('updatePredeterminedInstructions',id)
      },

      updateSubmit(){
        let updateBookKnow = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "data": this.updatePredeterminedInstructionsObj
        };
        this.$store.dispatch('updatePredeterminedInstructionsSubmit',updateBookKnow)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.ticketAttractionsValue);
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.updateDialog = false;
      }
    },
    created(){
      this.adminUserInfo = JSON.parse(sessionStorage.getItem('admin'));
      this.initTicketAttraction();
    }
  }
</script>
<style scoped>
  .search {
    margin: 20px;
  }
</style>



