<template>
  <div>
    <div id="wrap" class="clearfix">
      <h1 class="userClass"></h1>
      <!---->
      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-select v-model="outStatusId" placeholder="">
              <el-option
                v-for="item in outStatus"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select v-model="payStateId" placeholder="">
              <el-option
                v-for="item in payState"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->

      <el-table
        :data="foodStoreConfirnOrderList"
        style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label=":">
                <span>{{ props.row.fd_or_UserID }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_or_UserName }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_or_Phone }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_or_SumPrice }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_or_TicketCount }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_or_BeforeNotice }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_or_CreateTime }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_or_OrderID }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_or_OutStatus | getOutStatus }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_or_Password }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_or_PayState | payStatus  }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_or_PayTime }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_or_PayWay }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_or_TableID }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_or_UseState | useStatus }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_or_UseTime }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_or_Remark }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          prop="fd_or_UserName">
        </el-table-column>
        <el-table-column
          label=""
          align="center">
          <template slot-scope="scope">
            {{scope.row.fd_or_TicketCount ? scope.row.fd_or_TicketCount + '' : ''}}
          </template>
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          prop="fd_or_PayState">
          <template slot-scope="scope">
            {{ scope.row.fd_or_PayState | payStatus  }}
          </template>
        </el-table-column>
        <el-table-column
          label=""
          align="center">
          <template slot-scope="scope">
            {{scope.row.fd_or_OutStatus | getOutStatus}}
          </template>
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          prop="fd_or_TableID">
        </el-table-column>
        <el-table-column
          label=""
          align="center">
          <template slot-scope="scope">
            {{scope.row.fd_or_SumPrice ? scope.row.fd_or_SumPrice + '' : ''}}
          </template>
        </el-table-column>
        <el-table-column
          label=""
          width="300"
          align="center">
          <template slot-scope="scope">
            <el-button v-show="!scope.row.fd_or_OutStatus" type="success" size="mini"
                       @click="orderOk(scope.row.fd_or_OrderID)">
            </el-button>
            <el-button type="danger" size="mini"
                       @click="Delete(scope.row.fd_or_OrderID)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->

      <div class="block" style="text-align: right">
        <el-pagination
          :page-size="5"
          @current-change="handleCurrentChange"
          layout="prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>


    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([
      'foodStoreConfirnOrderList'
    ]),
    data() {
      return {
        userInfo: {},
        payState: [
          {
            value: 0,
            label: ''
          },
          {
            value: 1,
            label: ''
          }
        ],
        outStatus: [
          {
            value: 0,
            label: ''
          },
          {
            value: 1,
            label: ''
          }
        ],
        outStatusId: '',
        payStateId: '',
        total: 0,
      }
    },
    methods: {
      //      
      handleCurrentChange(num) {
        this.initData(this.outStatusId, this.payStateId, num);
      },
      //
      initData(outStatusId, payStateId, num) {

        let selectOrderInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "fd_or_OrderID": "",//ID
          "fd_or_UserID": "",//
          "fd_or_UserName": "",//
          "priceFrom": "",//
          "priceTo": "",//
          "fd_or_TouristTraderID": this.userInfo.sm_ai_ID,//
          "fd_or_Password": "",//
          "fd_or_OutStatus": outStatusId,// 0 1
          "fd_or_Phone": "",//
          "useTimeFrom": "",//
          "useTimeTo": "",//
          "createTimeFrom": "",//
          "createTimeTo": "",//
          "payTimeFrom": "",//
          "payTimeTo": "",//
          "fd_or_PayWay": "",//
          "fd_or_PayState": payStateId,// 0 1
          "fd_or_UseState": "",// 0 1
          "fd_or_IsDelete": "",// 0 1
          "rows": 5,
          "page": num ? num : 1
        };
        this.$store.dispatch('initFoodStoreConfirnOrder', selectOrderInfo)
          .then(total => {
            this.total = total;
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            })
          })
      },
      //
      search() {
        this.initData(this.outStatusId, this.payStateId)
      },
      //
      orderOk(id) {
        let sureOrderInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "orderID": id ? id : '',//
        };
        this.$store.dispatch('foodOrderOk', sureOrderInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            })
            this.initData(this.outStatusId, this.payStateId);
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            })
          })
      },
      //
      Delete(id) {
        let foodCancelOrder = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "2",
          "operateUserName": "",
          "pcName": "",
          "orderID": id ? id : '',
        };
        this.$store.dispatch('deleteFoodStoreConfirnOrder', foodCancelOrder)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            })
            this.initData();
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            })
          })
      },
    },
    created() {
      this.userInfo = JSON.parse(sessionStorage.getItem('admin'));
      this.initData()
    }
  }
</script>
<style scoped>
</style>



