<template>
  <div id="wrap" class="clearfix">
    <h1 class="userClass"></h1>
    <!---->
    <el-col :span="24" class="formSearch">
      <el-form :inline="true">
        <el-form-item>
          <span>:</span>
        </el-form-item>
        <!--<el-form-item>-->
          <!--<el-autocomplete-->
            <!--style="width: 150px"-->
            <!--v-model="userName"-->
            <!--:fetch-suggestions="querySearchAsync"-->
            <!--placeholder=""-->
            <!--@select="handleSelect"-->
          <!--&gt;</el-autocomplete>-->
        <!--</el-form-item>-->
        <el-form-item>
          <el-autocomplete
            style="width: 250px"
            v-model="userName"
            :fetch-suggestions="querySearchAsync"
            placeholder=""
            @select="handleSelect"
          ></el-autocomplete>
        </el-form-item>
        <el-form-item>
          <el-select v-model="userSearchID" placeholder="">
            <el-option
              v-for="item in adminProductLine"
              :key="item.ts_pt_ID"
              :label="item.ts_pt_Name"
              :value="item.ts_pt_ID">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addAdminProductFeatures"></el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <!---->
    <el-table
      v-loading="isLoading"
      :data="adminProductFeaturesList"
      style="width: 100%">
      <el-table-column
        prop="ts_gs_ID"
        align="center"
        label="">
      </el-table-column>
      <el-table-column
        prop="ts_gs_LinePrepareID"
        align="center"
        label="">
      </el-table-column>
      <el-table-column
        label=""
        align="center"
      >
        <template slot-scope="scope">
          <el-popover
            ref="popover2"
            placement="top-start"
            width="400"
            trigger="hover"
            :content="scope.row.ts_gs_Content">
          </el-popover>
          <el-button v-popover:popover2 size="small"></el-button>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="ts_gs_Type"
        label="">
      </el-table-column>
      <!--<el-table-column-->
        <!--align="center"-->
        <!--label="">-->
        <!--<template slot-scope="scope">-->
          <!--{{scope.row.ts_gs_IsDelete == 0 ? "" : ""}}-->
        <!--</template>-->
      <!--</el-table-column>-->
      <el-table-column
        align="center"
        label="">
        <template slot-scope="scope">
          <el-popover
            ref="popover2"
            placement="top-start"
            width="400"
            trigger="hover"
            :content="scope.row.ts_gs_Remark">
          </el-popover>
          <el-button v-popover:popover2 size="small"></el-button>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" width="300">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="updateAdminProductFeatures(scope.row.ts_gs_ID)">
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="deleteAdminProductFeatures(scope.row.ts_gs_ID)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <el-dialog title="" :visible.sync="addAdminProductFeaturesBox">
      <el-form :model="addAdminProductFeaturesObj">
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-select v-model="addAdminProductFeaturesObj.tsGsLinePrepareID" placeholder="">
            <el-option
              v-for="item in adminProductLine"
              :key="item.ts_pt_ID"
              :label="item.ts_pt_Name"
              :value="item.ts_pt_ID">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="addAdminProductFeaturesObj.tsGsContent"></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="addAdminProductFeaturesObj.tsGsType"></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="addAdminProductFeaturesObj.tsGsRemark"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addAdminProductFeaturesBox = false"> </el-button>
        <el-button type="primary" @click="addAdminProductFeaturesSubmit"> </el-button>
      </div>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="updateAdminProductFeaturesBox">
      <el-form :model="updateAdminProductFeaturesObj">
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-select v-model="updateAdminProductFeaturesObj.ts_gs_LinePrepareID" placeholder="">
            <el-option
              v-for="item in adminProductLine"
              :key="item.ts_pt_ID"
              :label="item.ts_pt_Name"
              :value="item.ts_pt_ID">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateAdminProductFeaturesObj.ts_gs_Content"></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateAdminProductFeaturesObj.ts_gs_Type"></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateAdminProductFeaturesObj.ts_gs_Remark"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="updateAdminProductFeaturesBox = false"> </el-button>
        <el-button type="primary" @click="updateAdminProductFeaturesSubmit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    name: '',
    computed: mapGetters([
      'adminTradeGoodList',
      'adminProductLine',
      'adminProductFeaturesList',
      'updateAdminProductFeaturesObj',
      'adminLineScheduleManagementId'
    ]),
    data() {
      return {
        ProductValue: '',
        ProductLineValue: '',
        isLoading: false,
        addAdminProductFeaturesObj: {
          tsGsLinePrepareID: '',
          tsGsContent: '',
          tsGsType: '',
          tsGsRemark: '',
        },
        formLabelWidth: '120px',
        updateAdminProductFeaturesBox: false,
        addAdminProductFeaturesBox: false,
        userName: '',
        GoodId: '',
        LineID: '',
        userSearchID: '',
      }
    },
    methods: {
      //
      handleSelect(item) {
        this.addAdminProductFeaturesObj.tsGsLinePrepareID = item.id;
        this.updateAdminProductFeaturesObj.ts_gs_LinePrepareID = item.id;
        this.userName = item.value;
        let options = {
          loginUserID: 'huileyou',
          loginUserPass: 123,
          goodID: item.id ? item.id : ''
        };
        this.$store.dispatch('initAdminProductLine', options)
      },
      loadAll(num, name) {
        return new Promise((relove, reject) => {
          var _this = this;
          let obj = JSON.parse(sessionStorage.getItem('admin'))
          let options = {
            "loginUserID": "huileyou",
            "loginUserPass": "123",
            "operateUserID": "",
            "operateUserName": "",
            "tradeID": obj.sm_ai_AgentID ? obj.sm_ai_AgentID : '',
            "userID": "",
            "pcName": "",
            "ID": '',
            'isDelete': 0,
            "page": 1,
            "rows": 100
          };
          this.$store.dispatch('initAdminTradeGoodList', options)
          .then((data) => {
            relove(data)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        })
      },
      querySearchAsync(queryString, cb) {
        this.loadAll(1, queryString).then(data => {
          var  data = data.data
          data = data.map(item => {
            return {
              id: item.ta_tg_ID,
              value: item.ta_tg_Title
            }
          })
          this.restaurants = data;
          clearTimeout(this.timeout);
          this.timeout = setTimeout(() => {
            cb(this.restaurants);
          }, 10);
        })
      },
      //
      initData(id) {
        var getGoodSpecial = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          'isDelete':0,
          "lineID": id ? id : ''
        };
        this.isLoading = true;
        this.$store.dispatch('initAdminProductFeatures', getGoodSpecial)
          .then(() => {
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.isLoading = false;
      },
      search() {
        if(!this.userSearchID){
          this.$notify({
            message: '',
            type: 'error'
          });
          return;
        }
        this.initData(this.userSearchID)
      },
      //
      searchInitData() {
        if(this.adminLineScheduleManagementId){
          this.initData(this.adminLineScheduleManagementId)
        }
      },
      //
      addAdminProductFeatures() {
        this.$store.commit('setTranstionFalse');
        this.addAdminProductFeaturesBox = true;
      },
      addAdminProductFeaturesSubmit() {
        var addGoodSpecial = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "data": {
            "ts_gs_LinePrepareID": this.addAdminProductFeaturesObj.tsGsLinePrepareID,
            "ts_gs_Content": this.addAdminProductFeaturesObj.tsGsContent,
            "ts_gs_Type": this.addAdminProductFeaturesObj.tsGsType,
            "ts_gs_Remark": this.addAdminProductFeaturesObj.tsGsRemark,
          }
        };
        this.$store.dispatch('initAddAdminProductFeaturesObj', addGoodSpecial)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.addAdminProductFeaturesObj.tsGsLinePrepareID);
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.addAdminProductFeaturesBox = false;
      },
      //
      updateAdminProductFeatures(id) {
        //
        this.$store.commit('setTranstionFalse');
        this.updateAdminProductFeaturesBox = true;
        this.$store.commit('initUpdateAdminProductFeatures', id)
      },
      updateAdminProductFeaturesSubmit() {
        var updateGoodSpecial = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "data": this.updateAdminProductFeaturesObj
        };
        this.$store.dispatch('updateAdminProductFeaturesSubmit', updateGoodSpecial)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.updateAdminProductFeaturesObj.ts_gs_LinePrepareID);
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.updateAdminProductFeaturesBox = false;
      },
      //
      deleteAdminProductFeatures(id) {
        var deleteGoodSpecial = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "productID": id
        }
        this.$store.dispatch('deleteAdminProductFeatures', deleteGoodSpecial)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            if(this.userSearchID){
              this.initData(this.userSearchID);
            }else{
              this.initData(this.adminLineScheduleManagementId);
            }
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
    },
    mounted() {
      this.searchInitData();
    }
  }
</script>
<style>
</style>



