<template>
  <div>
    <div id="wrap" class="clearfix">
      <h1 class="userClass"></h1>

      <!---->

      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-select v-model="storeId" placeholder="">
              <el-option
                v-for="item in foodStoreInformtionList"
                :key="item.fd_sf_ID"
                :label="item.fd_sf_ProductName"
                :value="item.fd_sf_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <div class="block">
              <el-date-picker
                v-model="searchTime"
                :picker-options="pickerOptions"
                type="daterange"
                range-separator=""
                start-placeholder=""
                end-placeholder=""
                value-format="yyyy-MM-dd">
              </el-date-picker>
            </div>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search"></el-button>
            <el-button type="primary" @click="add"></el-button>
            <el-button type="danger" @click="Delete"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->

      <el-table
        :data="foodStoreOrderingTimeList"
        style="width: 100%"
        @selection-change="selete"
      >
        <el-table-column
          type="selection"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="fd_rtt_ID"
          align="center"
          label="">
        </el-table-column>
        <el-table-column
          prop="fd_sf_ProductName"
          align="center"
          label="">
        </el-table-column>
        <el-table-column
          prop="fd_rtt_Time"
          align="center"
          label="">
        </el-table-column>
        <el-table-column
          prop="fd_sfr_RoomName"
          align="center"
          label="">
        </el-table-column>
        <el-table-column
          prop="fd_rtt_Date"
          align="center"
          label="">
        </el-table-column>
        <el-table-column
          prop="fd_rtt_TableState"
          align="center"
          label="">
        </el-table-column>
      </el-table>

      <!---->

      <div class="block" style="text-align: right">
        <el-pagination
          :page-size="10"
          @current-change="handleCurrentChange"
          layout="prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>

      <!---->

      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="addOptions.storeID" placeholder="">
              <el-option
                v-for="item in foodStoreInformtionList"
                :key="item.fd_sf_ID"
                :label="item.fd_sf_ProductName"
                :value="item.fd_sf_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <div class="block">
              <el-date-picker
                v-model="addTime"
                :picker-options="pickerOptions"
                type="daterange"
                range-separator=""
                start-placeholder=""
                end-placeholder=""
                value-format="yyyy-MM-dd">
              </el-date-picker>
            </div>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->

      <el-dialog title="" :visible.sync="deleteDialog">
        <el-form :model="addOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="addOptions.storeID" placeholder="">
              <el-option
                v-for="item in foodStoreInformtionList"
                :key="item.fd_sf_ID"
                :label="item.fd_sf_ProductName"
                :value="item.fd_sf_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <div class="block">
              <el-date-picker
                v-model="addOptions.dateFrom"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="">
              </el-date-picker>
            </div>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <div class="block">
              <el-date-picker
                v-model="addOptions.dateTo"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="">
              </el-date-picker>
            </div>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="deleteDialog = false"> </el-button>
          <el-button type="primary" @click="deleteSubmit"> </el-button>
        </div>
      </el-dialog>


    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([
      'foodStoreInformtionList',
      'foodStoreOrderingTimeList'
    ]),
    data() {
      return {
        storeId: '',
        searchTime: '',
        addTime: '',
        total: 0,
        addDialog: false,
        deleteDialog: false,
        addOptions: {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "storeID": "",//
          "dateFrom": "",//
          "dateTo": "",//
        },
        formLabelWidth: '120px',
        deleteObj: {},
        deleteData: [],
        pickerOptions: {
          disabledDate(time) {
            return (time.getTime()+86400000) < Date.now();
          },
        }
      }
    },
    methods: {
      selete(selection) {
        this.deleteData = selection;
      },
      //      
      handleCurrentChange(num) {
        this.initData(this.searchTime[0], this.searchTime[1], this.storeId, num);
      },
      //
      initData(form, to, id, num) {
        let selectRoomTableTimeInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "page": num ? num : 1,
          "rows": "10",
          "fd_rtt_ID": "",//
          "dateFrom": form ? form : '',//
          "dateTo": to ? to : '',//
          "fd_rtt_TableState": "",//
          "fd_rtt_FrontID": id ? id : ''//
        };
        this.$store.dispatch('initFoodStoreOrderingTime', selectRoomTableTimeInfo)
          .then(total => {
            this.total = total;
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            })
          })
      },
      //
      search() {
        this.initData(this.searchTime[0], this.searchTime[1], this.storeId, 1);
      },
      //
      add() {
        this.$store.commit('setTranstionFalse');
        this.addDialog = true;
      },
      //
      addSubmit() {
        this.addOptions.dateFrom = this.addTime[0];
        this.addOptions.dateTo = this.addTime[1];
        this.$store.dispatch('addFoodStoreOrderingTime', this.addOptions)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            })
            this.initData();
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            })
          })
        this.addDialog = false;
      },
      //
      Delete() {
        if (!this.deleteData.length) {
          this.$notify({
            message: '!!',
            type: 'error'
          })
          return
        }
        ;
        for (let i = 0; i < this.deleteData.length; i++) {
          this.deleteSubmit(this.deleteData[i].fd_rtt_ID);
        }

      },
      //
      deleteSubmit(id) {
        let deleteRoomTableTimeInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "fd_rtt_ID": id,//
          }
        };
        this.$store.dispatch('deleteFoodStoreOrderingTime', deleteRoomTableTimeInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            })
            this.initData();
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            })
          })
      },
    },
    created() {
      this.initData();
    }
  }
</script>
<style scoped>
</style>



