<template>
  <div>
    <div id="wrap" class="clearfix">
      <h1 class="userClass"></h1>
      <!---->
      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-input v-model="siteName"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search"></el-button>
          </el-form-item>
        </el-form>
      </el-col>


      <!---->

      <el-table
        :data="ticketAttractionsList"
        v-loading="isLoading"
        style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="():">
                <span>{{ props.row.tm_ts_Code }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Name }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_tt_Name }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_tm_ID }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_GreatName }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_CountrieName }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_ProviceName }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_CityName }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_ContryName }}</span>
              </el-form-item>
              <el-form-item label=":">
                <img v-for="item,index in props.row.tm_ts_ShowImage" :src="item" width="300" height="150"
                     style="margin-top: 10px;">
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Introduce }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Detailedintroduction }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Address }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Opentime }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Time }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Phone }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Longitude }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Latitude }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_IsHot }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_IsOversea }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_IsSeasonChoice }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_SuggestPrice }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_CreateTime }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.tm_ts_Remark }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="tm_ts_Name">
        </el-table-column>
        <el-table-column
          label=""
          prop="tm_ts_Phone">
        </el-table-column>
        <el-table-column label="" align="center" style="width: 1000px">
          <template slot-scope="scope">
            <el-button type="success" size="mini" @click="apply(scope.row)">
              <template>{{scope.row.showTopLabel}}</template>
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <div class="block" style="float: right;">
        <el-pagination
          @current-change="handleCurrentChange"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>

    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([
      'ticketAttractionsList'
    ]),
    data() {
      return {
        siteName: '',
        total: 0,
        adminUserInfo: {},
        isLoading: false,
        page:1,
      }
    },
    methods: {
      //
      handleCurrentChange(num) {
        this.page = num;
        this.initData('')
      },


      initData(name) {
        this.isLoading = true;
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "tm_ts_Code": "",    //
          "tm_ts_Name": name ? name : '',//
          "tm_ts_TradeInfoID": this.adminUserInfo.sm_ai_ID,//
          "tm_ts_IsDelete": 0,//
          "tm_ts_IsPass": "",//(012)
          "tm_ts_ShowTop": "",//01
          "tm_ts_IsHot": "",//01)
          "tm_ts_ThemeTypeID": "",//
          "page": this.page ? this.page : 1,
          "rows": 5
        };
        this.$store.dispatch('initTicketAttractions', options)
          .then(total => {
            this.total = total;
            this.isLoading = false;
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },

      search() {
        this.initData(this.siteName)
      },
      //
      apply(obj) {
        if (obj.tm_ts_ShowTop == 0) {
          obj.tm_ts_ShowTop = 1;
        } else {
          obj.tm_ts_ShowTop = 0;
        }
        let updateTourSiteIsShowTop = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "tm_ts_Code": obj.tm_ts_Code,
          "tm_ts_ShowTop": obj.tm_ts_ShowTop,
        }
        this.$store.dispatch('applyShowHomePage', updateTourSiteIsShowTop)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData('');
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      }
    },
    created() {
      this.adminUserInfo = JSON.parse(sessionStorage.getItem('admin'));
      this.initData('')
    }
  }
</script>
<style scoped>
</style>



