<script>
  import {mapGetters} from 'vuex'
  export default{
    name: '',
    data(){
      return {
        userInfo: {},
        loginUserID: "huileyou",
        isOff:true,
        value:0,
        isShow:true,
        isShow1:true,
        ImageURL:[],
        ImageURL1:[],
        isLoading:false,
        total:0,
        formLabelWidth:'120px',
        adApply:'',
        addDialog:false,//
        updateDialog:false,//
        addOptions:{
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            // "sm_aa_ID": "",//
            "sm_aa_AdviertiseTypeID": "",//
            "sm_aa_LocationID": "",//
            "sm_aa_AgentID": this.userInfo,//
            "sm_aa_Image": "",//
            "sm_aa_Describe": "",//
            "sm_aa_ProductID": "",//ID
            // "sm_aa_CreateTime": "",//
            // "sm_aa_PassState": "",//
            // "sm_aa_PassTime": "",//
            "sm_aa_OperateCode": "",//
            "sm_aa_FailedReason": "",//
            "sm_aa_Remark": "",//
          }
        },//
        passOptions: [{
          value: '3',
          label: ''
        }, {
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }],
        updatePassOptions:[{
        value: '3',
        label: ''
      }, {
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }],
      }
    },
    computed: mapGetters([
      'adTypeApplyAllList',
      'adApplyList',
      'updateAdApplyObj',
      'adPositionAllList'
    ]),
    created(){
      this.userInfo = JSON.parse(sessionStorage.getItem('admin')).sm_ai_AgentID;
      this.initData()
      this.adPositionAll()
      this.initAdTypeAllList()
    },
    methods: {
      //
      initData(){
        let options = {
          "loginUserID": this.loginUserID,
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "sm_aa_ID": "",//
          "sm_aa_AdviertiseTypeID": "",//
          "sm_aa_LocationID": "",//
          "sm_aa_AgentID": "",//
          "sm_aa_Image": "",//
          "sm_aa_Describe": "",//
          "sm_aa_ProductID": "",//ID
          "sm_aa_CreateTime": "",//
          "sm_aa_PassState": "",//
          "sm_aa_PassTime": "",//
          "sm_aa_OperateCode": "",//
          "sm_aa_FailedReason": "",//
          "sm_aa_Remark": "",//
        }
        this.isLoading = true;
        this.$store.dispatch('initAdApply',options)
          .then(total=>{
            this.total = total;
            this.isLoading = false;
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      //
      adPositionAll(){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "sm_pi_ID": "",//
          "sm_pi_LocationNo": "",//
          "sm_pi_MaxNo": "",//
          "sm_pi_Name":'',//
          "sm_pi_Remark": "",//
        }
        this.$store.dispatch('initAdPositionAll',options)
          .then(total=>{
            this.total = total;
            this.isLoading = false;
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      //
      initAdTypeAllList(){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "sm_at_ID": "",//
          "sm_at_Name":"",//
          "sm_at_CreateTime": "",//
          "sm_at_Cost": "",//
          "sm_at_Remark": "",//
          "page": 1,
          "rows": 10
        };
        this.$store.dispatch('initAdTypeAllList',options)
          .then(total=>{
            this.total = total;
            this.isLoading = false;
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },

      //
      search(){
        this.initData()
      },
      //
      Add(){
        //
        let obj = this.addOptions.data;
        for(var i in obj){
          obj[i]=""
        };
        this.addDialog = true;
        this.$store.commit('setTranstionFalse');
        this.uploaNode();
      },
      //
      addSubmit(){
        //
        this.addOptions.data.sm_aa_Image = this.ImageURL.join(',');
        this.$store.dispatch('AddAdApply',this.addOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });

            this.initData()
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.addDialog = false;
      },
      //
      OnChange(){
        if(this.addOptions.data.sm_aa_PassState==1){
          this.isShow=false;
        }
        else this.isShow=true;
      },
      OnChangeUpdate(){
        if(this.updateAdApplyObj.sm_aa_PassState==1){
          this.isShow1=false;
        }
        else this.isShow1=true;
      },
      //
      uploadImg(file) {
        return new Promise((relove, reject) =>{
          lrz(file)
            .then(data => {
              relove(data.base64.split(',')[1])
            })
        })
      },
      uploaNode() {
        this.ImageURL = [];
        this.ImageURL1 = [];
        setTimeout(() => {
          if (this.$refs.upload) {
            this.$refs.upload.addEventListener('change', data => {
              for (var i = 0; i < this.$refs.upload.files.length; i++) {
                this.uploadImg(this.$refs.upload.files[i]).then(data => {
                  this.$store.dispatch('adApplyUploadAdminImgs', {
                    imageData: data
                  })
                    .then(data => {
                      if (data) {
                        this.ImageURL.push(data.data);
                      } else {
                        this.$notify({
                          message: '!',
                          type: 'error'
                        });
                      }
                    })
                })
              }
            })
          }
          if (this.$refs.upload1) {
            this.$refs.upload1.addEventListener('change', data => {
              for (var i = 0; i < this.$refs.upload1.files.length; i++) {
                this.uploadImg(this.$refs.upload1.files[i]).then(data => {
                  this.$store.dispatch('adApplyUploadAdminImgs', {
                    imageData: data
                  })
                    .then(data => {
                      if (data) {
                        this.ImageURL1.push(data.data);
                      } else {
                        this.$notify({
                          message: '!',
                          type: 'error'
                        });
                      }
                    })
                })
              }
            })
          }
        }, 30)
      },
      //
      Update(id){
        this.updateDialog = true;
        this.$store.commit('setTranstionFalse');
        this.$store.commit('initUpdateAdApply',id);
        this.updateAdApplyObj.sm_aa_PassState="";
        this.uploaNode();
      },
      //
      updateAdApplySubmit(){
        let updateOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          // "data": this.updateAdApplyObj
          "data": {
            "sm_aa_ID": this.updateAdApplyObj.sm_aa_ID,//
            "sm_aa_AdviertiseTypeID": this.updateAdApplyObj.sm_aa_AdviertiseTypeID,//
            "sm_aa_LocationID": this.updateAdApplyObj.sm_aa_LocationID,//
            "sm_aa_AgentID": this.userInfo,//
            "sm_aa_Image": this.updateAdApplyObj.sm_aa_Image,//
            "sm_aa_Describe": this.updateAdApplyObj.sm_aa_Describe,//
            "sm_aa_ProductID": this.updateAdApplyObj.sm_aa_ProductID,//ID
            "sm_aa_PassState": this.updateAdApplyObj.sm_aa_PassState,//
            "sm_aa_OperateCode": this.updateAdApplyObj.sm_aa_OperateCode,//
            "sm_aa_FailedReason": this.updateAdApplyObj.sm_aa_FailedReason,//
            "sm_aa_Remark": this.updateAdApplyObj.sm_aa_Remark,//
          }

        };
        console.log(updateOptions)
        updateOptions.data.sm_aa_Image = this.ImageURL1.join(',');
        this.$store.dispatch('UpdateAdApplyObj',updateOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.adApply)
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          });
        this.updateDialog = false
      },
      //
      Delete(id){
        let deleteOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "sm_aa_ID": id,//
          }
        };
        this.$store.dispatch('DeleteAdApply',deleteOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.adApply)
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      }
    },
  }
</script>
<style scoped>

</style>



