<template>
  <div>
    <div id="wrap" class="clearfix">

      <h1 class="userClass"></h1>
      <!---->
      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-select v-model="foodStoreName" placeholder="">
              <el-option
                v-for="item in foodStoreInformtionList"
                :key="item.fd_sf_ID"
                :label="item.fd_sf_ProductName"
                :value="item.fd_sf_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search"></el-button>
            <el-button type="primary" @click="add"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->
      <el-table

        :data="foodStoppingPlaceList"
        style="width: 100%">
        <el-table-column
          label=""
          prop="fd_sc_ID">
        </el-table-column>
        <el-table-column
          label=""
          prop="fd_sc_SeatNo">
        </el-table-column>
        <el-table-column
          label=""
          prop="fd_sc_ShopID">
        </el-table-column>
        <el-table-column
          label=""
          prop="fd_sc_LockStatus">
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="update(scope.row)"></el-button>

            <el-button size="mini" type="danger" @click="deleteSubmit(scope.row.fd_sc_ID)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->


      <div class="block" style="text-align: right">
        <el-pagination
          :page-size="5"
          @current-change="handleCurrentChange"
          layout="prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>

      <!---->

      <el-dialog title="" :visible.sync="addDialog">
        <el-form>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.data.fd_sc_SeatNo"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="addOptions.data.fd_sc_ShopID" placeholder="">
              <el-option
                v-for="item in foodStoreInformtionList"
                :key="item.fd_sf_ID"
                :label="item.fd_sf_ProductName"
                :value="item.fd_sf_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->


      <el-dialog title="" :visible.sync="updateDialog">
        <el-form :model="updateObj">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateObj.fd_sc_ID" :disabled="isOff"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateObj.fd_sc_SeatNo"></el-input>
          </el-form-item>

          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="updateObj.fd_sc_ShopID" placeholder="">
              <el-option
                v-for="item in foodStoreInformtionList"
                :key="item.fd_sf_ID"
                :label="item.fd_sf_ProductName"
                :value="item.fd_sf_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="updateObj.fd_sc_LockStatus" placeholder="">
              <el-option
                v-for="item in Status"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateDialog = false"> </el-button>
          <el-button type="primary" @click="updateSubmit"> </el-button>
        </div>
      </el-dialog>

    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([
      'foodStoppingPlaceList',
      'foodStoreInformtionList',
    ]),
    data() {
      return {
        isOff: true,
        foodStoreName: "",
        updateObj: {},
        addDialog: false,
        formLabelWidth: '120px',
        updateDialog: false,
        addOptions: {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            // "fd_sc_ID": "",//
            "fd_sc_SeatNo": "",//
            "fd_sc_ShopID": "",//
          }
        },
        Status: [{
          value: 0,
          label: ''
        }, {
          value: 1,
          label: ''
        }, {
          value: 2,
          label: ''
        }],
        total: 0,

      }
    },
    methods: {
//      
      handleCurrentChange(num) {
        this.initData(this.foodStoreName, num)
      },
      //
      initData(name, num) {
        if (!name) {
          this.$notify({
            message: '',
            type: 'error'
          })
          return
        }
        let selectFoodStoppingPlace = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "page": num ? num : 1,
          "rows": "10",
          "fd_sc_ID": "",//
          "fd_sc_ShopID": name,//
          "fd_sc_SeatNo": "",//
          "fd_sc_LockStatus": "",//

        };
        this.$store.dispatch('initFoodStoppingPlace', selectFoodStoppingPlace)
          .then(total => {
            this.total = total;
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      //
      search() {
        this.initData(this.foodStoreName)
      },
      //
      add() {
        this.addOptions.data.fd_sc_SeatNo = "";
        this.addOptions.data.fd_sc_ShopID = "";
        this.$store.commit('setTranstionFalse');
        this.addDialog = true;
        // this.addOptions.fd_sf_TradeID = this.userInfo.sm_ai_ID;
      },
//      
      addSubmit() {
        this.$store.dispatch('addFoodStoppingPlace', this.addOptions)
          .then((suc) => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.foodStoreName)
          }, (err) => {
            this.$notify({
              message: err,
              type: "error"
            })
          });
        this.addDialog = false;
      },
      //
      update(rowData) {

        //
        for (let i = 0; i < this.Status.length; i++) {
          if (this.Status[i].label == rowData.fd_sc_LockStatus) {
            rowData.fd_sc_LockStatus = this.Status[i].value;
          }
        }
        this.updateObj = rowData;
        this.$store.commit('setTranstionFalse');
        this.updateDialog = true;
      },
      //
      updateSubmit() {
        let updateOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",

          "data": {
            "fd_sc_ID": this.updateObj.fd_sc_ID,//
            "fd_sc_ShopID": this.updateObj.fd_sc_ShopID,//
            "fd_sc_SeatNo": this.updateObj.fd_sc_SeatNo,//
            "fd_sc_LockStatus": this.updateObj.fd_sc_LockStatus,//
          }
        };
        this.$store.dispatch('updateFoodStoppingPlace', updateOptions)
          .then((suc) => {
            this.$notify({
              message: suc,
              type: "success"
            });
            this.initData(this.foodStoreName);
          }, (err) => {
            this.$notify({
              message: err,
              type: "error"
            });
          });
        this.updateDialog = false;
      },
      //
      deleteSubmit(id) {
        let deleteOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "fd_sc_ID": id,//
          }

        }
        this.$store.dispatch('deleteFoodStoppingPlace', deleteOptions)
          .then((suc) => {
            this.$notify({
              message: suc,
              type: "success"
            });
            this.initData(this.foodStoreName);
          }, (err) => {
            this.$notify({
              message: err,
              type: "error"
            })
          });
      }

    },

  }
</script>
<style scoped>
</style>



