<template>
  <div>
    <div id="wrap" class="clearfix">
      <h1 class="userClass"></h1>

      <!---->

      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-select v-model="storeId" placeholder="">
              <el-option
                v-for="item in foodStoreInformtionList"
                :key="item.fd_sf_ID"
                :label="item.fd_sf_ProductName"
                :value="item.fd_sf_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search"></el-button>
            <el-button type="primary" @click="add"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->

      <el-table
        :data="foodStoreRecommendList"
        style="width: 100%">

        <el-table-column
          prop="fd_sf_ProductName"
          label=""
          align="center">
        </el-table-column>

        <el-table-column
          prop="fd_if_Name"
          label=""
          align="center">
        </el-table-column>

        <el-table-column
          prop="fd_if_Price"
          label="()"
          align="center">
        </el-table-column>

        <el-table-column
          label=""
          align="center">
          <template slot-scope="scope">
            <img v-lazy="scope.row.fd_if_Image" width="128" height="80"
                 @click="displayBigPicture(scope.row.fd_if_Image)">
          </template>
        </el-table-column>

        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="update(scope.row)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="Delete(scope.row.fd_if_ID)">
            </el-button>
          </template>
        </el-table-column>

      </el-table>

      <!---->

      <el-dialog
        title=""
        :visible.sync="bigPictureDialog"
        width="50%">
        <img v-lazy="imgUrl" width="100%">
        <span slot="footer" class="dialog-footer">
          <el-button @click="bigPictureDialog = false"> </el-button>
        </span>
      </el-dialog>

      <!---->
      <div class="block" style="text-align: right">
        <el-pagination
          :page-size="5"
          @current-change="handleCurrentChange"
          layout="prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>

      <!---->

      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="addOptions.fd_if_StoreFrontID" placeholder="">
              <el-option
                v-for="item in foodStoreInformtionList"
                :key="item.fd_sf_ID"
                :label="item.fd_sf_ProductName"
                :value="item.fd_sf_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <a href="javascript:;" class="file">
              <input type="file" name="" ref="upload" accept="image/*">
            </a>
            <img v-lazy="addOptions.fd_if_Image" v-show="addOptions.fd_if_Image" width="128" height="80">
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.fd_if_Name"></el-input>
          </el-form-item>
          <el-form-item label="():" :label-width="formLabelWidth">
            <el-input v-model="addOptions.fd_if_Price"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->

      <el-dialog title="" :visible.sync="updateDialog">
        <el-form :model="updateObj">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="updateObj.fd_if_StoreFrontID" placeholder="">
              <el-option
                v-for="item in foodStoreInformtionList"
                :key="item.fd_sf_ID"
                :label="item.fd_sf_ProductName"
                :value="item.fd_sf_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <a href="javascript:;" class="file">
              <input type="file" name="" ref="upload" accept="image/*">
            </a>
            <p></p>
            <img v-lazy="addOptions.fd_if_Image" v-show="addOptions.fd_if_Image" width="128" height="80">
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateObj.fd_if_Name"></el-input>
          </el-form-item>
          <el-form-item label="():" :label-width="formLabelWidth">
            <el-input v-model="updateObj.fd_if_Price"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateDialog = false"> </el-button>
          <el-button type="primary" @click="updateSubmit"> </el-button>
        </div>
      </el-dialog>


    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([
      'foodStoreInformtionList',
      'foodStoreRecommendList'
    ]),
    data() {
      return {
        storeId: '',
        total: 0,
        formLabelWidth: '120px',
        addDialog: false,
        updateDialog: false,
        addOptions: {
          "fd_if_StoreFrontID": "",//
          "fd_if_Image": "",//
          "fd_if_Price": "",//
          "fd_if_Name": "",//
        },
        updateObj: {},
        imgUrl: '',
        bigPictureDialog: false,
      }
    },
    methods: {
      //      
      handleCurrentChange(num) {
        this.initData(this.storeId, num);
      },
      //
      uploadImg(file) {
        return new Promise(function (relove, reject) {
          lrz(file)
            .then(data => {
              relove(data.base64.split(',')[1])
            })
        })
      },

      uploaNode() {
        this.addOptions.fd_if_Image = '';
        setTimeout(() => {
          if (this.$refs.upload) {
            this.$refs.upload.addEventListener('change', data => {
              for (var i = 0; i < this.$refs.upload.files.length; i++) {
                this.uploadImg(this.$refs.upload.files[i]).then(data => {
                  this.$store.dispatch('foodUploadAdminImgs', {
                    imageData: data
                  })
                    .then(data => {
                      if (data) {
                        this.addOptions.fd_if_Image = data.data;
                      } else {
                        this.$notify({
                          message: '!',
                          type: 'error'
                        });
                      }
                    })
                })
              }
            })
          }
        }, 30)
      },
      //
      initData(id, num) {
        if (!id) {
          this.$notify({
            message: '',
            type: 'error'
          })
          return
        }
        let selectIntroduceFoodInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "fd_if_ID": "",//
          "fd_if_StoreFrontID": id,//
          "priceFrom": "",//
          "priceTo": "",
          "fd_if_Name": "",//
          "page": num ? num : 1,
          "rows": "5",
        };
        this.$store.dispatch('initFoodStoreRecommend', selectIntroduceFoodInfo)
          .then(total => {
            this.total = total;
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            })
          })
      },
      //
      search() {
        this.initData(this.storeId)
      },

      displayBigPicture(urlData) {
        this.$store.commit('setTranstionFalse');
        this.bigPictureDialog = true;
        this.imgUrl = urlData;
      },
      //
      add() {
        this.uploaNode();
        this.$store.commit('setTranstionFalse');
        this.addDialog = true;
      },
      //
      addSubmit() {
        let insertIntroduceFoodInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.addOptions
        };
        this.$store.dispatch('addFoodStoreRecommend', insertIntroduceFoodInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            })
            this.initData(this.storeId)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            })
          })
        this.addDialog = false;
      },
      //
      update(rowData) {
        this.updateObj = rowData;
        this.$store.commit('setTranstionFalse');
        this.updateDialog = true;
        this.uploaNode();
      },
      //
      updateSubmit() {
        if (this.addOptions.fd_if_Image) {
          this.updateObj.fd_if_Image = this.addOptions.fd_if_Image;
        }
        ;
        let updateIntroduceFoodInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.updateObj
        };
        this.$store.dispatch('updateFoodStoreRecommend', updateIntroduceFoodInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            })
            this.initData(this.storeId)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            })
          })
        this.updateDialog = false;
      },
      //
      Delete(id) {
        let deleteIntroduceFoodInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "fd_if_ID": id ? id : '',//
          }
        };
        this.$store.dispatch('deleteFoodStoreRecommend',deleteIntroduceFoodInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            })
            this.initData(this.storeId)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            })
          })
      },
    },
  }
</script>
<style scoped>
</style>



