<template>
  <div>
    <div id="wrap" class="clearfix">
      <h1 class="userClass"></h1>
      <!---->
      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-input v-model="roomName"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search"></el-button>
            <el-button type="primary" @click="add"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->
      <el-table
        :data="foodStoreInformtionList"
        style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="">
                <span>{{ props.row.fd_sf_ID }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.fd_sf_TypeID }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.fd_py_MansName }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.fd_sf_ProductName }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.fd_py_Name }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.fd_sf_Address }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.fd_sf_Lng }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.fd_sf_Lat }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.fd_sf_Provice }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.fd_sf_City }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.fd_sf_AvgPrice }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.fd_sf_OpenTime }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.fd_sf_Phone }}</span>
              </el-form-item>
              <el-form-item label="WAFI">
                <span>{{ props.row.fd_sf_HasWafi }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.fd_sf_TradeID }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.fd_sf_PassStatus }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="fd_sf_ProductName">
        </el-table-column>
        <el-table-column
          label=""
          prop="fd_sf_Phone">
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            {{scope.row.fd_sf_AvgPrice}}
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="update(scope.row)"></el-button>
            <el-button size="mini" type="danger" @click="Delete(scope.row.fd_sf_ID)"></el-button>
            <el-button size="mini" type="success" @click="recommendShop(scope.row.fd_sf_ID)"></el-button>

          </template>
        </el-table-column>
      </el-table>

      <!---->

      <div class="block" style="text-align: right">
        <el-pagination
          :page-size="5"
          @current-change="handleCurrentChange"
          layout="prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>

      <!---->

      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="addOptions.fd_sf_TypeID" placeholder="">
              <el-option
                v-for="item in storefrontTypeList"
                :key="item.propertyID"
                :label="item.propertyName"
                :value="item.propertyID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="addOptions.fd_sf_MansID" placeholder="">
              <el-option
                v-for="item in numberOfMealsList"
                :key="item.propertyID"
                :label="item.propertyName"
                :value="item.propertyID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.fd_sf_ProductName"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.fd_sf_Address"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.fd_sf_Lng"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.fd_sf_Lat"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="addOptions.fd_sf_Provice" placeholder="" @change="changeCity">
              <el-option
                v-for="item in foodProcinceList"
                :key="item.sm_af_AreaID"
                :label="item.sm_af_AreaName"
                :value="item.sm_af_AreaName">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="addOptions.fd_sf_City" placeholder="">
              <el-option
                v-for="item in foodCityList"
                :key="item.sm_af_AreaID"
                :label="item.sm_af_AreaName"
                :value="item.sm_af_AreaName">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.fd_sf_AvgPrice"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.fd_sf_OpenTime"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="addOptions.fd_sf_Phone"></el-input>
          </el-form-item>
          <el-form-item label="WAFI:" :label-width="formLabelWidth">
            <el-select v-model="addOptions.fd_sf_HasWafi" placeholder="">
              <el-option
                v-for="item in isWifi"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </div>
      </el-dialog>

      <!---->

      <el-dialog title="" :visible.sync="updateDialog">
        <el-form :model="updateObj">
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="updateObj.fd_sf_TypeID" placeholder="">
              <el-option
                v-for="item in storefrontTypeList"
                :key="item.propertyID"
                :label="item.propertyName"
                :value="item.propertyID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="updateObj.fd_sf_MansID" placeholder="">
              <el-option
                v-for="item in numberOfMealsList"
                :key="item.propertyID"
                :label="item.propertyName"
                :value="item.propertyID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateObj.fd_sf_ProductName"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateObj.fd_sf_Address"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateObj.fd_sf_Lng"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateObj.fd_sf_Lat"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="updateObj.fd_sf_Provice" placeholder="" @change="changeCity">
              <el-option
                v-for="item in foodProcinceList"
                :key="item.sm_af_AreaID"
                :label="item.sm_af_AreaName"
                :value="item.sm_af_AreaName">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="updateObj.fd_sf_City" placeholder="">
              <el-option
                v-for="item in foodCityList"
                :key="item.sm_af_AreaID"
                :label="item.sm_af_AreaName"
                :value="item.sm_af_AreaName">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateObj.fd_sf_AvgPrice"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateObj.fd_sf_OpenTime"></el-input>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
            <el-input v-model="updateObj.fd_sf_Phone"></el-input>
          </el-form-item>
          <el-form-item label="WAFI:" :label-width="formLabelWidth">
            <el-select v-model="updateObj.fd_sf_HasWafi" placeholder="">
              <el-option
                v-for="item in isWifi"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="updateDialog = false"> </el-button>
          <el-button type="primary" @click="updateSubmit"> </el-button>
        </div>
      </el-dialog>

    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([
      'foodStoreInformtionList',
      'numberOfMealsList',
      'storefrontTypeList',
      'foodProcinceList',
      'foodCityList',
    ]),
    data() {
      return {
        userInfo: {},
        total: 0,
        addDialog: false,
        addOptions: {
          "fd_sf_TypeID": "",
          "fd_sf_MansID": "",
          "fd_sf_ProductName": "",
          "fd_sf_Address": "",
          "fd_sf_Lng": "",
          "fd_sf_Lat": "",
          "fd_sf_Provice": "",
          "fd_sf_City": "",
          "fd_sf_AvgPrice": "",
          "fd_sf_OpenTime": "",
          "fd_sf_Phone": "",
          "fd_sf_HasWafi": "",
          "fd_sf_TradeID": "",
        },
        formLabelWidth: '120px',
        updateDialog: false,
        isWifi: [
          {
            value: 0,
            label: ''
          },
          {
            value: 1,
            label: ''
          }

        ],
        proviceId: '',
        roomName: '',
        updateObj: {},
      }
    },
    methods: {
//      
      handleCurrentChange(num) {
        this.initData('', num)
      },
      //
      initProvince() {
        let getAreaProvice = {
          "areaPid": 3337
        };
        this.$store.dispatch('initFoodProcince', getAreaProvice)
      },
      //
      changeCity(name) {
        this.proviceId = this.foodProcinceList.filter(item => {
          if (item.sm_af_AreaName == name) {
            return true;
          }
          return false;
        })[0].sm_af_AreaID
        let getAreaProvice = {
          "areaPid": this.proviceId
        };
        this.$store.dispatch('initFoodCity', getAreaProvice)
      },
      //
      initData(name, num) {
        let selectStoreFrontInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "fd_sf_ID": "",//
          "fd_sf_TypeID": "",//
          "fd_sf_MansID": "",//
          "fd_sf_ProductName": name ? name : '',// like
          "fd_sf_Provice": "",//
          "fd_sf_City": "",//
          "priceFrom": "",//
          "priceTo": "",//
          "fd_sf_Phone": "",//
          "fd_sf_TradeID": this.userInfo.sm_ai_ID,//
          "page": num ? num : 1,
          "rows": "5",
        };
        this.$store.dispatch('initFoodStoreInformtion', selectStoreFrontInfo)
          .then(total => {
            this.total = total;
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      //
      search() {
        this.initData(this.roomName, 1)
      },
      //
      add() {
        this.$store.commit('setTranstionFalse');
        this.addDialog = true;
        this.addOptions.fd_sf_TradeID = this.userInfo.sm_ai_ID;
      },
//      
      addSubmit() {
        let insertStoreFrontInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": this.addOptions
        };
        this.$store.dispatch('addFoodStoreInformation', insertStoreFrontInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData();
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.addDialog = false;
      },
      //
      update(rowData) {
        console.log(rowData)
        this.updateObj = rowData
        this.$store.commit('setTranstionFalse');
        this.updateDialog = true;
        this.updateObj.fd_sf_HasWafi = '';
      },
      //
      updateSubmit() {
        let updateStoreFrontInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "fd_sf_ID": this.updateObj.fd_sf_ID,
            "fd_sf_TypeID": this.updateObj.fd_sf_TypeID,
            "fd_sf_MansID": this.updateObj.fd_sf_MansID,
            "fd_sf_ProductName": this.updateObj.fd_sf_ProductName,
            "fd_sf_Address": this.updateObj.fd_sf_Address,
            "fd_sf_Lng": this.updateObj.fd_sf_Lng,
            "fd_sf_Lat": this.updateObj.fd_sf_Lat,
            "fd_sf_Provice": this.updateObj.fd_sf_Provice,
            "fd_sf_City": this.updateObj.fd_sf_City,
            "fd_sf_AvgPrice": this.updateObj.fd_sf_AvgPrice,
            "fd_sf_OpenTime": this.updateObj.fd_sf_OpenTime,
            "fd_sf_Phone": this.updateObj.fd_sf_Phone,
            "fd_sf_HasWafi": this.updateObj.fd_sf_HasWafi,
            "fd_sf_TradeID": this.updateObj.fd_sf_TradeID,
          }
        };
        this.$store.dispatch('updateFoodStoreInformtionSubmit', updateStoreFrontInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData();
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.updateDialog = false;
      },
      //
      Delete(id) {
        let deleteStoreFrontInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "fd_sf_ID": id,//
          }
        };
        this.$store.dispatch('deleteFoodStoreInformtion', deleteStoreFrontInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData();
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      //
      recommendShop(id) {
        let insertIntroduceShopInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "fd_is_ShopID": id ? id : '',//
          }
        };
        this.$store.dispatch('recommendShopSubmit',insertIntroduceShopInfo)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData();
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },

    },
    created() {
      this.userInfo = JSON.parse(sessionStorage.getItem('admin'))
      this.initData();
      this.initProvince()
    }
  }
</script>
<style scoped>
</style>



